import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';
import { pipe } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  baseUrl = API + '/api/users';

  constructor(private http: HttpClient) { }

  listUsers() {
    return this.http.get<any>(this.baseUrl)
      .pipe(
        tap()
      )
  }

  userLogin(email: string, password: string) {
    let postBody = {
      "email": email,
      "password": password
    }
    return this.http.get<any>(`${this.baseUrl}/token`, { headers: postBody })
      .pipe(
        tap()
      )
  }

  getUserDetails(token) {
    let getedHeader =  {
      'Authorization': `Bearer ${token}`
    };
    return this.http.get<any>(`${this.baseUrl}/details`, { headers: getedHeader })
      .pipe(
        tap()
      )
  }

  deleteUser(userId) {
    return this.http.delete<any>(`${this.baseUrl}/${userId}`)
      .pipe(
        tap()
      )
  }

  getPermissions(userId) {
    return this.http.get<any>(`${this.baseUrl}/permissions/${userId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getUsersGroups() {
    return this.http.get<any>(`${this.baseUrl}/groups`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getRoles(groupId) {
    return this.http.get<any>(`${this.baseUrl}/roles/group/${groupId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createUser(body) {
    return this.http.post<any>(`${this.baseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  updateUser(userId, body) {
    return this.http.put<any>(`${this.baseUrl}/${userId}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

}
