import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';

@Injectable({
  providedIn: 'root'
})
export class CashiersService {

  baseUrl = API + '/api/cashier'

  constructor(private http: HttpClient) { }

  getCashiersList(storeId: string) {
    return this.http.get<any>(`${this.baseUrl}/getCashiers/${storeId}?Skip=0&Take=10`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getOneCashier(cashierId) {
    return this.http.get<any>(`${this.baseUrl}/${cashierId}`)
      .pipe(
        tap()
      )
  }

  getCashiersFilteredById(storeId, filter) {
    return this.http.get<any>(`${this.baseUrl}/getCashiers?StoreID=${storeId}&Skip=0&Take=10&CashierId=${filter}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createCashier(body) {
    return this.http.post<any>(`${this.baseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  updateCashier(cashierId, body) {
    return this.http.put<any>(`${this.baseUrl}/${cashierId}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  deleteCashier(cashierId) {
    return this.http.delete<any>(`${this.baseUrl}/${cashierId}`, { headers: HEADER })
  }


}
