import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { NavigationStart, Router } from '@angular/router';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {

  @Output() sideBarLoaded = new EventEmitter();

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }


  private checkboxFlag = false;
  private lastItemActivated: any;
  private lastItemId: string;


  private items = []

  private routes: string[];
  private pages = {
    'dashboard': 0,
    'contratos': 1,
    'lojas': 2,
    'caixas': 3,
    'descontos': 4,
    'usuarios': 5,
    'relatorios-transacionais': 6,
    'relatorios-logs-descontos': 7
  }

  constructor(
    private router: Router,
    private userService: UserService
  ) {
    router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        this.selectItemByRoute(event.url)
      }
    })
  }

  ngOnInit() {
    this.verifyPermissions()
  }

  verifyPermissions() {
    let userPermissions
    this.userService.getPermissions(this.user.userId).subscribe(res => {
      if (res.sucesso) {
        this.sideBarLoaded.emit(true)
      }
      res.data.find(exp => {
        if (exp.path === 'dashboard') {
          this.items.push(
            {
              title: 'Dashboard',
              icon: 'fas fa-chart-bar',
              route: 'dashboard'
            }
          )
        }
      })
      res.data.find(exp => {
        if (exp.path === 'contratos') {
          this.items.push(
            {
              title: 'Contratos',
              icon: 'fas fa-file-alt',
              route: 'contratos/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'lojas') {
          this.items.push(
            {
              title: 'Lojas',
              icon: 'fas fa-store',
              route: 'lojas/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'caixas') {
          this.items.push(
            {
              title: 'Caixas',
              icon: 'fas fa-cash-register',
              route: 'caixas/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'descontos') {
          this.items.push(
            {
              title: 'Descontos',
              icon: 'fas fa-percent',
              route: 'descontos/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'usuarios') {
          this.items.push(
            {
              title: 'Usurios',
              icon: 'fas fa-users',
              route: 'usuarios/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'relatorios-transacionais') {
          this.items.push(
            {
              title: 'Relatrios Transacionais',
              icon: 'fas fa-list-alt',
              route: 'relatorios-transacionais/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'relatorios-logs-descontos') {
          this.items.push(
            {
              title: 'Relatrios Logs Descontos',
              icon: 'fas fa-history',
              route: 'relatorios-logs-descontos/listar'
            }
          )
        }
      })
    })

  }
  
  
  
  
  
  
  
  
  
  

  
  
  
  
  
  

  ngAfterViewInit() {
    this.selectItemByRoute(this.router.url)
  }

  selectItemByRoute(url) {
    this.routes = url.split('/')
    this.routes.splice(0, 1) // tira o '/'        
    this.toggleItemClicked(this.pages[this.routes[0]])
  }


  toggleItemClicked(index) {
    let item = document.getElementById(index)
    if (this.lastItemActivated) {
      this.lastItemActivated.getElementsByClassName('selected')[0].classList.remove('active')
      this.lastItemActivated.getElementsByClassName('icon')[0].classList.remove('icon-clicked')
      if (this.lastItemActivated.getElementsByClassName('title')[0])
        this.lastItemActivated.getElementsByClassName('title')[0].classList.remove('title-selected')

    }

    item.getElementsByClassName('icon')[0].classList.add('icon-clicked')
    item.getElementsByClassName('selected')[0].classList.add('active')
    if (item.getElementsByClassName('title')[0])
      item.getElementsByClassName('title')[0].classList.add('title-selected')

    this.lastItemActivated = item
    this.lastItemId = index;

  }

  barChanged() {
    if (this.lastItemActivated && this.checkboxFlag) {
      setTimeout(() => {
        document.getElementById(this.lastItemId).getElementsByClassName('title')[0].classList.add('title-selected')
      }, 0)
    }
  }

}
