import { Component, OnInit } from '@angular/core';
import { ContractsService } from 'src/app/services/http-requests';
import { HttpService } from '../../services/http/http.service';
import { ActivatedRoute, Router } from '@angular/router';
import swal from 'sweetalert2';
import { SwalService } from 'src/app/services/swal/swal.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';
@Component({
  selector: 'app-contrato-listar',
  templateUrl: './contrato-listar.component.html',
  styleUrls: ['./contrato-listar.component.scss']
})
export class ContratoListarComponent implements OnInit {

  canView = false

  page
  contracts;
  loading;
  storesList = false
  timeout = null;
  lastCodeOrSocialReason = null
  lastContractStatus = null
  preSetContract

  filterInput = {
    codeOrSocialReason: '',
    contractStatus: 'Todos'
  }
  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  contractsTitle =
    [
      'Contrato',
      'Situao Contrato',
      'CNPJ',
      'Razo Social',
      'Status',
      'Habilitado Em'
    ]

  constructor(
    private contractService: ContractsService,
    private route: ActivatedRoute,
    private swalService: SwalService,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }
  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for(let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if(permissionsArr.includes('contratos')) {
          localStorage.setItem('currentUri', 'contratos/listar')
          this.canView = true
          this.router.navigateByUrl('/contratos')
          this.loadContracts()
          this.initLoadInput()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  initLoadInput() {
    this.route.params.subscribe(params => {
      this.preSetContract = params.contractId
    })
  }

  loadContracts() {

    this.contractService.getAllContractsInfo().subscribe(res => {
      this.contracts = res.data.contracts
      this.storesList = true
    })
  }

  deleteContract(id) {
    this.swalService.deleteContractSwal(id)
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.contractService.getAllContractsInfo().subscribe(res => {
        for (let i = 0; i < res.data.contracts.length; i++) {
          if (res.data.contracts[i].razao_social.includes(evt)) {
            returned.push(res.data.contracts[i])
          }
        }
      })
      this.storesList = true
      this.contracts = returned
    }, 500)
  }

  getStatusToFilter(evt) {
    this.contractService.getAllContractsInfo().subscribe(res => {
      if (evt === '2' || evt === '0') {
        let parsedStatus = parseInt(evt)
        let returned: any[] = []
        for (let i = 0; i < res.data.contracts.length; i++) {
          if (parsedStatus == res.data.contracts[i].situacao_contrato) {
            returned.push(res.data.contracts[i])
          }
        }
        this.contracts = returned
        this.storesList = true
      } else {
        this.contracts = res.data.contracts
        this.storesList = true
      }
    })
  }

}
