import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';

@Injectable({
  providedIn: 'root'
})
export class StoresService {

  baseUrl = API + '/api/store';

  constructor(private http: HttpClient) { }

  getContractsList(contractId: string) {
    return this.http.get<any>(`${this.baseUrl}/getAllStoresIdByContractTokPag?contractTokPag=${contractId}`)
      .pipe(
        tap()
      )
  }

  gatOneStore(storeId) {
    return this.http.get<any>(`${this.baseUrl}/${storeId}`)
      .pipe(
        tap()
      )
  }

  getStoresFilteredWithContract(contractId) {
    let skip = 0;
    let take = 10;
    return this.http.get<any>(`${this.baseUrl}/getStoresByContractTokPag?contractTokPag=${contractId}&skip=${skip}&take=${take}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createStore(body) {
    return this.http.post<any>(`${this.baseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  updateStore(storeId, body) {
    return this.http.put<any>(`${this.baseUrl}/${storeId}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  deleteStore(storeId) {
    return this.http.delete<any>(`${this.baseUrl}/${storeId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

}
