import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { API } from 'src/app/services/config';
import { ContractsService } from 'src/app/services/http-requests';
import { UserService } from 'src/app/services/http-requests/user/user.service';
import { HttpService } from 'src/app/services/http/http.service';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-cadastrar',
  templateUrl: './cadastrar.component.html',
  styleUrls: ['./cadastrar.component.scss']
})
export class CadastrarComponent implements OnInit {

  dropdownList
  contractToPost: any[] = []
  contracts
  UserFuncId
  isLoaded = false

  isCreate;
  validCNPJ = false;
  validCPF = false;
  previousRoute: string;
  guid;
  contractId;
  socialReason;
  guid_empresa;
  idStore;
  store: FormGroup;
  isSaving: boolean = false;
  filterInput = {
    contractId: 'Identificao do Contrato'
  }
  contractsIds: any;
  contractsIdsSelecao
  lastContractId: string;
  private qtdPassadas: number = 0
  selectedItems = [];
  dropdownSettings = {};
  groups;
  functionList;
  getedFunctionId
  getedFunctionName
  contratosArray = [];
  isEdit = false;
  semfuncao;
  grupoId;
  funcaoId;
  userToEdit;
  funcaoIdUsuario;
  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private httpService: HttpService,
    private contractService: ContractsService,
    private userService: UserService,
    private swalService: SwalService
  ) {
    this.store = fb.group({
      'cpf': [null, Validators.required],
      'name': [null, Validators.required],
      'email': [null, Validators.required],
      'senha': [null, Validators.required],
      'funcao': [null, Validators.required],
      'grupo': [null, Validators.required],
      'contractIds': [null, Validators.required],
    })
  }


  ngOnInit() {
    localStorage.setItem('currentUri', 'usuarios/listar')
    this.getGroups();
    this.getcontracts()
    this.isEdit = false;
    this.isLoaded = true


    this.isCreate = this.router.url.split('/')[3].indexOf('idUsuario');

    this.route.params.subscribe(async params => {

      let dependence = JSON.parse(params.dependence)
      this.userToEdit = dependence.idUsuario

    })
    if (this.isCreate < 0) {
      this.route.params.subscribe(async params => {

        let dependence = JSON.parse(params.dependence)
        this.contractId = dependence.contractId
        this.guid = dependence.guid
        this.filterInput.contractId = this.contractId
        this.contractsIds = await this.httpService.get(`${API}/api/store/getStoresContractsIds/` + localStorage.getItem('idusuario'));
        this.contractsIdsSelecao = this.contractsIds.data;
      })
    } else {
      this.getUserInfo()
      this.semfuncao = true;

      this.route.params.subscribe(async params => {

        this.contractsIds = await this.httpService.get(`${API}/api/store/getStoresContractsIds/` + localStorage.getItem('idusuario'));
        this.contractsIdsSelecao = this.contractsIds.data;

      })
      this.isEdit = true;

    }
  }

  getUserInfo() {
    this.isLoaded = false;
    this.userService.listUsers().subscribe(res => {
      if (res.data) {
        this.isEdit = true
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].id_usuario === this.userToEdit) {
            let preraredArr: any[] = [];
            if (res.data[i].contratos) {
              for (let contI = 0; contI < res.data[i].contratos.length; contI++) {
                preraredArr.push(
                  {
                    id_empresa: res.data[i].contratos[contI].id_empresa,
                    company_name: res.data[i].contratos[contI].nome_loja
                  }
                )
              }
            }
            this.selectedItems = preraredArr
            this.onItemSelect(preraredArr)
            this.onSelectAll(preraredArr)
            this.store.get('name').setValue(res.data[i].nome)
            this.store.get('email').setValue(res.data[i].nome_usuario)
            this.store.get('grupo').setValue(res.data[i].grupo_funcao.id_grupo)
            if (res.data[i].grupo_funcao.id_grupo) {
              this.userService.getRoles(res.data[i].grupo_funcao.id_grupo).subscribe(funcRes => {
                for (let funI = 0; funI < funcRes.data.length; funI++) {
                  if (res.data[i].grupo_funcao.id_grupo_funcao === funcRes.data[funI].id_grupo_funcao) {
                    this.store.get('funcao').setValue(funcRes.data[funI].id_grupo_funcao)
                    this.getedFunctionId = funcRes.data[funI].id_grupo_funcao
                    this.getedFunctionName = funcRes.data[funI].funcao.role_name
                  }
                }
              })
            }
            setTimeout(() => { this.isLoaded = true }, 3000)
          }
        }
      }
    })
  }

  getcontracts() {
    this.contractService.getAllContractsInfo().subscribe(res => {
      this.prepareStoresMultiSelect(res.data.contracts)
    })
  }

  getGroups() {
    this.userService.getUsersGroups().subscribe(res => {
      this.groups = res.data
    })
  }

  getRoles(evt) {
    this.userService.getRoles(evt).subscribe(res => {
      this.functionList = res.data
    })
  }

  fillFields(res) {
    this.store.controls['cpf'].setValue(res.cpf)
    this.store.controls['name'].setValue(res.nome)
    this.store.controls['email'].setValue(res.email)
    this.store.controls['senha'].setValue(res.senha)
    this.store.controls['funcao'].setValue(res.funcao)
    this.store.controls['grupo'].setValue(res.grupo)
    this.store.controls['contractIds'].setValue(res.grupo)
    this.validCPF = true
  }

  async btnSalvar() {
    let storeRequest: any = {
      nome: this.store.get('name').value,
      nome_usuario: this.store.get('email').value,
      senha: this.store.get('senha').value,
      id_grupo_funcao: this.store.get('funcao').value,
      contratos: this.contractToPost
    }

    if (!this.isEdit) {
      this.userService.createUser(storeRequest).subscribe(res => {
        if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        } else {
          this.swalService.success(res.mensagem, this.router.navigate(['/usuarios/listar']))

          this.store.controls['name'].setValue('')
          this.store.controls['email'].setValue('')
          this.store.controls['senha'].setValue('')
          this.store.controls['funcao'].setValue('')
          this.store.controls['grupo'].setValue('')
          this.contratosArray = []
          this.selectedItems = []
        }
      })
    } else {
      this.userService.updateUser(this.userToEdit, storeRequest).subscribe(res => {
        if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        } else {
          this.swalService.success(res.mensagem, this.router.navigate(['/usuarios/listar']))

          this.store.controls['name'].setValue('')
          this.store.controls['email'].setValue('')
          this.store.controls['senha'].setValue('')
          this.store.controls['funcao'].setValue('')
          this.store.controls['grupo'].setValue('')
          this.contratosArray = []
          this.selectedItems = []
          this.router.navigate(['usuarios/listar']);
        }
      })
    }

  }
  prepareStoresMultiSelect(evt) {
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'id_empresa',
      textField: 'company_name',
      selectAllText: 'Marcar todos',
      unSelectAllText: 'Desmarcar todos',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
    let preparationArr: any[] = []
    for (let i = 0; i < evt.length; i++) {
      preparationArr.push(
        { id_empresa: evt[i].id_empresa, company_name: evt[i].razao_social }
      )
    }
    this.dropdownList = preparationArr
  }

  onItemSelect(item: any) {
    this.contractToPost.push({ id_empresa: item.id_empresa })
  }

  onSelectAll(items: any) {
    this.contractToPost = []
    for (let i = 0; i < items.length; i++) {
      this.contractToPost.push({ id_empresa: items[i].id_empresa })
    }
  }
}