import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'app-not-permissioned',
  templateUrl: './not-permissioned.component.html',
  styleUrls: ['./not-permissioned.component.scss']
})
export class NotPermissionedComponent implements OnInit {

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  permisions = []

  constructor(
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.getUserPermissions()
  }

  getUserPermissions() {
    this.userService.getPermissions(this.user.userId).subscribe(res => {
      res.data.find(exp => {
        if (exp.path === 'dashboard') {
          this.permisions.push(
            {
              title: 'Dashboard',
              icon: 'fas fa-chart-bar',
              route: '/dashboard'
            }
          )
        }
      })
      res.data.find(exp => {
        if (exp.path === 'contratos') {
          this.permisions.push(
            {
              title: 'Contratos',
              icon: 'fas fa-file-alt',
              route: '/contratos/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'lojas') {
          this.permisions.push(
            {
              title: 'Lojas',
              icon: 'fas fa-store',
              route: '/lojas/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'caixas') {
          this.permisions.push(
            {
              title: 'Caixas',
              icon: 'fas fa-cash-register',
              route: '/caixas/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'descontos') {
          this.permisions.push(
            {
              title: 'Descontos',
              icon: 'fas fa-percent',
              route: '/descontos/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'usuarios') {
          this.permisions.push(
            {
              title: 'Usurios',
              icon: 'fas fa-users',
              route: '/usuarios/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'relatorios-transacionais') {
          this.permisions.push(
            {
              title: 'Relatorios Transacionais',
              icon: 'fas fa-list-alt',
              route: '/relatorios-transacionais/listar'
            }
          )
        }
      })

      res.data.find(exp => {
        if (exp.path === 'relatorios-logs-descontos') {
          this.permisions.push(
            {
              title: 'Relatorios Logs Descontos',
              icon: 'fas fa-history',
              route: '/relatorios-logs-descontos/listar'
            }
          )
        }
      })
    })
  }


}
