import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';
import { Router } from '@angular/router';
import { ReportsService, ContractsService, StoresService } from 'src/app/services/http-requests';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'app-reports-logs-list',
  templateUrl: './reports-logs-list.component.html',
  styleUrls: ['./reports-logs-list.component.scss']
})
export class ReportsLogsListComponent implements OnInit {
  page

  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  logsResults
  showList = false;

  contracts
  storesList
  usersList

  filteredStoreId

  constructor(
    private router: Router,
    private logsService: ReportsService,
    private contractsService: ContractsService,
    private storesService: StoresService,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if (permissionsArr.includes('relatorios-logs-descontos')) {
          localStorage.setItem('currentUri', 'relatorios-transacionais/listar')
          this.canView = true
          this.router.navigateByUrl('/relatorios-logs-descontos')
          this.getcontracts()
          this.loadUsers()
          this.getResultsWithoutParams()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  getResultsWithoutParams() {
    this.logsService.getLogs().subscribe(res => {
      this.logsResults = res.data.logDiscounts
      this.showList = true
    })
  }

  loadUsers() {
    this.userService.listUsers().subscribe(res => {
      this.usersList = res.data
    })
  }

  getResultsWithStoreId(storeId) {
    this.logsService.getLogs().subscribe(res => {
      if (storeId) {
        let response
        response = res.data.logDiscounts.filter(r => storeId == r.loja.id_loja)
        this.logsResults = response
      } else {
        this.getResultsWithoutParams()
      }
    })
  }

  getResultsWithUserId(userId) {
    this.logsService.getLogs().subscribe(res => {
      if (userId) {
        let response
        response = res.data.logDiscounts.filter(r => userId == r.usuario.id_usuario)
        this.logsResults = response
      } else {
        this.getResultsWithoutParams()
      }
    })
  }

  getResultsWithAllParams(storeId, userId) {
    this.logsService.getLogs().subscribe(res => {
      if (storeId && userId) {
        let response
        response = res.data.logDiscounts.filter(
          r => storeId == r.loja.id_loja && userId == r.usuario.id_usuario
        )
        this.logsResults = response
      } else if (storeId && !userId) {
        this.getResultsWithStoreId(storeId)
      } else if (!storeId && userId) {
        this.getResultsWithUserId(userId)
      } else if (!storeId && !userId) {
        this.getResultsWithoutParams()
      }
    })
  }

  getcontracts() {
    this.contractsService.getAllContractsInfo().subscribe(res => {
      this.contracts = res.data.contracts
    })
  }

  saveSelectedContract(contractId) {
    this.storesService.getContractsList(contractId).subscribe(res => {
      this.storesList = res.data
    })
  }

  saveSelectedStore(evt) {
    this.getResultsWithStoreId(evt)
  }

  saveSelectedUser(evt) {
    this.getResultsWithUserId(evt)
  }

  filterWithAllParams(storeId, userId) {
    this.getResultsWithAllParams(storeId, userId)
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.logsService.getLogs().subscribe(res => {
        for (let i = 0; i < res.data.logDiscounts.length; i++) {
          if (res.data.logDiscounts[i].desconto_nome.includes(evt)) {
            returned.push(res.data.logDiscounts[i])
          }
        }
      })
      this.logsResults = returned
      this.showList = true
    }, 500)
  }

}
