import { Component, OnInit } from '@angular/core';
import { ContractsService, StoresService } from 'src/app/services/http-requests';
import { DescontosService } from 'src/app/services/http-requests/descontos/descontos.service';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'app-desconto',
  templateUrl: './desconto.component.html',
  styleUrls: ['./desconto.component.scss']
})
export class DescontoComponent implements OnInit {
  contractList

  paramsList = {
    contractId: '',
    storeId: '',
    discountId: ''
  }

  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }


  listPage = true;

  constructor(
    private contractService: ContractsService,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    localStorage.setItem('currentUri', 'descontos/listar')
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for(let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if(permissionsArr.includes('descontos')) {
          this.canView = true
          this.router.navigateByUrl('/descontos')
          this.getContractList()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  getContractList() {
    this.contractService.getAllContractsInfo().subscribe(res => {
      this.contractList = res.data.contracts
    })
  }

  changePage($event) {
    this.listPage = $event
  }

  getContractIdToEdit($event) {
    this.paramsList.contractId = $event
  }

  getstoreIdToEdit($event) {
    this.paramsList.storeId = $event
  }
  getDiscountIdToEdit($event) {
    this.paramsList.discountId = $event
  }

}


