import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http/http.service';

import PrintJS from 'print-js';
import { ActivatedRoute, Router } from '@angular/router';
import b64toBlob from 'b64-to-blob';
import { API } from 'src/app/services/config';
import Swal from 'sweetalert2'
import { ContractsService, StoresService, CashiersService } from 'src/app/services/http-requests';
import { SwalService } from 'src/app/services/swal/swal.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';


@Component({
  selector: 'app-caixa-listar',
  templateUrl: './caixa-listar.component.html',
  styleUrls: ['./caixa-listar.component.scss']
})
export class CaixaListarComponent implements OnInit {
  preSetContract
  preSetStore

  selectedContractId
  selectedStoreId

  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  contractList
  storesList
  cashiersList
  shwoList = false;

  cashiers = []
  totalItems = null;
  timeout = null;
  modalQrOpened = false;
  actualImage;
  skip = 0;
  readonly take = 10;

  modalDeleteOpened = false;
  qrCodeSelected;
  descriptionSelected;
  cashierSelected: string;
  filterLoaded: boolean;
  isContractLoaded: boolean = false
  contractsIds = undefined
  storesIds = undefined
  storeValue
  objectKeys = Object.keys
  p: number;

  filterInput = {
    code: '',
    storeId: 'Identificao da Loja',
    contractId: 'Identificao do Contrato'
  }
  lastStoreId
  lastContractId
  lastCode

  cashiersTitle =
    [
      'Id. Caixa',
      'Descrio do Caixa',
      'Hash do Caixa',
      'Status',
    ]

  cashierTest = {
    cashierId: '100-10',
    cashierDescription: 'Primeiro Caixa',
    cashierSituation: 'Criado',
    cashierHash: 'Hash do Caixa',
    status: 'Inativo',
  }

  constructor(
    private httpService: HttpService,
    private route: ActivatedRoute,
    private contractService: ContractsService,
    private storesService: StoresService,
    private cashiersService: CashiersService,
    private swalService: SwalService,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if (permissionsArr.includes('caixas')) {
          localStorage.setItem('currentUri', 'caixas/listar')
          this.canView = true
          this.router.navigateByUrl('/caixas')
          this.getRouteParams()
          this.getContractList()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  getRouteParams() {
    this.route.params.subscribe((params) => {
      this.preSetContract = params.contractId
      this.preSetStore = params.storeId
      if (this.preSetStore) {
        this.getCashiers(this.preSetStore)
      }
    })
  }

  getContractList() {
    this.contractService.getAllContractsInfo().subscribe(res => {
      this.contractList = res.data.contracts
    })
  }

  getStoresList(evt) {
    this.storesService.getStoresFilteredWithContract(evt).subscribe(res => {
      this.storesList = res.data;
      this.filterLoaded = true
    })
  }

  getCashiers(evt) {
    if (this.filterInput.storeId != this.lastStoreId || this.filterInput.code != this.lastCode) {
      this.p = 1;
      this.skip = 0;
    }

    this.selectedStoreId = evt

    this.cashiersService.getCashiersList(evt).subscribe(res => {
      this.cashiersList = res.data.cashier
      this.shwoList = true
      this.filterLoaded = true
    })
  }

  filterCashiers(storeId, filter) {
    if (this.filterInput.storeId != this.lastStoreId || this.filterInput.code != this.lastCode) {
      this.p = 1;
      this.skip = 0;
    }

    this.cashiersService.getCashiersFilteredById(storeId, filter).subscribe(res => {
      this.cashiersList = res.cashier
      this.filterLoaded = true
    })
  }

  deleteCashier(evt) {
    this.swalService.deleteCashierSwal(evt)
  }

  async openQrCode(qrcode, cashierDescription) {
    this.modalQrOpened = !this.modalQrOpened;
    this.cashierSelected = cashierDescription;

    this.qrCodeSelected = `https://api.qrserver.com/v1/create-qr-code/?size=200x200&data=${qrcode}`
  }

  insertDataToHTML() {
    return `
    <html>

  <head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <meta http-equiv="X-UA-Compatible" content="ie=edge">
  </head>

  <body>
  <div style="text-align: center; margin-top: 7.35cm; margin-left: 5.5cm">
    <div style="width: 10cm; height: 15cm; border: 3px black dotted; font-size: 0.9em">
      <div style="width: 90%; margin-top: 1cm; ">
        <div style="font-weight: 600; margin-bottom: 0.5cm; text-align: center; font-size: initial">Voc dever
          disponibilizar um Cdigo QR
          em
          cada
          caixa/checkout, conforme os seguintes passos:</div>

        <div style="margin-left: 0.5cm; margin-bottom: 0.2cm;">1)<span style="margin-left: 0.5cm;">Imprima e recorte o
            papel no local indicado.</span></div>

        <div style="margin-left: 0.5cm; margin-bottom: 0.2cm;">2)<span style="margin-left: 0.5cm;">Encaixe o papel na
            parte de trs do suporte
            de acrlico mantendo o Cdigo QR
            visvel.
          </span>
        </div>

        <div style="margin-left: 0.5cm; margin-bottom: 0.2cm;">3)<span style="margin-left: 0.5cm;">Cole o suporte em um
            local visvel e
            lembre-se de conferir se o Cdigo QR
            corresponde ao caixa/checkout.</span></div>
      </div>
      <img id="qr_code" src="${this.qrCodeSelected}" style="position: absolute; top:6.14cm; left:8.76cm; height: 3.85cm; width: 3.85cm;">
      <div style="margin-top: 5.5cm; text-align: center; font-weight: 700;" id="descricao_caixa">${this.cashierSelected}</div>
    </div>
  </div>
    
  </body>
</html>
    `
  }

  async printQrCode() {

    const res = await this.httpService.post(`https://wallet-pdf.azurewebsites.net/api/servicos/pdf/geraretiqueta`, {
      ConteudoHtml: this.insertDataToHTML(),
      TamanhoPagina: "A4",
      OrientacaoPagina: "PORTRAIT"
    })


    let byteCharacters = window.atob(res);
    let byteNumbers = new Array(byteCharacters.length);
    for (var i = 0; i < byteCharacters.length; i++) {
      byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    let byteArray = new Uint8Array(byteNumbers);

    let blob = new Blob([byteArray], { type: 'application/pdf' });

    if (window.navigator && window.navigator.msSaveOrOpenBlob) {
      window.navigator.msSaveOrOpenBlob(blob);
    } else {
      const pdf = URL.createObjectURL(blob)
      window.open(pdf);
    }

    this.modalQrOpened = false;
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.cashiersService.getCashiersList(this.selectedStoreId).subscribe(res => {
        for (let i = 0; i < res.data.cashier.length; i++) {
          if (res.data.cashier[i].descricao_caixa.includes(evt)) {
            returned.push(res.data.cashier[i])
          }
        }
      })
      this.cashiersList = returned
      this.shwoList = true
      this.filterLoaded = true
    }, 500)
  }

  getStatusToFilter(evt) {
    let returned: any[] = []
    this.cashiersService.getCashiersList(this.selectedStoreId).subscribe(res => {
      if (evt === '2' || evt === '0') {
        let parsedStatus = parseInt(evt)
        let returned: any[] = []
        for (let i = 0; i < res.data.cashier.length; i++) {
          if (parsedStatus == res.data.cashier[i].status_caixa) {
            returned.push(res.data.cashier[i])
          }
        }
        this.cashiersList = returned
        this.shwoList = true
        this.filterLoaded = true
      } else {
        this.cashiersList = res.data.cashier
        this.shwoList = true
        this.filterLoaded = true
      }
    })
  }
}
