import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './guard/auth.guard';
import { PdfComponent, LojaTransferirComponent } from './components';
import { LoginComponent, DashboardComponent, ContratoListarComponent, ContratoCadastroEditComponent, LojaListarComponent, LojaCadastroEditComponent, CaixaListarComponent, CaixaCadastroEditComponent, RelatoriosComponent, DescontoComponent, ReportsLogsListComponent, ConfigurationPageComponent } from './pages';
import { ListarComponent, CadastrarComponent } from './pages/usuarios';
import { LoadingComponent } from './shared/loading/loading.component';
import { NotPermissionedComponent } from './shared/not-permissioned/not-permissioned.component';


const routes: Routes = [

  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'contratos',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: ContratoListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'cadastro',
        component: ContratoCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'editar/:dependence',
        component: ContratoCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'lojas',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: LojaListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'listar/:idContract',
        component: LojaListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'cadastro/:dependence',
        component: LojaCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'editar/:dependence',
        component: LojaCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'loja-transferir/:idStore',
        component: LojaTransferirComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'caixas',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: CaixaListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'listar/:dependence',
        component: CaixaListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'cadastro/:dependence',
        component: CaixaCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'editar/:dependence',
        component: CaixaCadastroEditComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'descontos',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: DescontoComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'usuarios',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: ListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'listar/:idContract',
        component: ListarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'cadastro',
        component: CadastrarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: 'editar/:dependence',
        component: CadastrarComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'relatorios-transacionais',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: RelatoriosComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'relatorios-logs-descontos',
    canActivate: [AuthGuard],
    children: [
      {
        path: 'listar',
        component: ReportsLogsListComponent,
        canActivate: [AuthGuard]
      },
      {
        path: '',
        redirectTo: 'listar',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: 'configuracoes',
    component: ConfigurationPageComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'carregamento',
    component: LoadingComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'not-allowed',
    component: NotPermissionedComponent,
    canActivate: [AuthGuard]
  }];



@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
