import { Injectable } from '@angular/core';
import swal from 'sweetalert2';
import { DescontosService } from '../http-requests/descontos/descontos.service';
import { Router } from '@angular/router';
import { ContractsService, StoresService, CashiersService } from '../http-requests';
import { UserService } from '../http-requests/user/user.service';

@Injectable({
  providedIn: 'root'
})
export class SwalService {

  constructor(
    private router: Router,
    private descontoService: DescontosService,
    private contractService: ContractsService,
    private storeService: StoresService,
    private cashierService: CashiersService,
    private userService: UserService
  ) { }

  success(text, action) {
    swal({
      title: "Sucesso!",
      text: text,
      type: "success"
    }).then(
      (act) => { action }
    );
  }

  error(text) {
    swal({
      title: "Erro!",
      text: text,
      type: "error"
    });
  }

  deleteContractSwal(id) {
    swal({
      title: 'Tem certeza?',
      text: `Gostaria de deletar o contrato: ${id}`,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Sim, deletar!'
    })
      .then((result) => {
        if (result.value) {
          this.router.navigate(['/carregamento']);
          this.deleteContract(id)
        } else {
          swal('Cancelado', `o contrato: ${id} no ser deletado`)
        }
      })
  }

  deleteStoreSwal(id) {
    swal({
      title: 'Tem certeza?',
      text: `Gostaria de deletar a loja: ${id}`,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Sim, deletar!'
    })
      .then((result) => {
        if (result.value) {
          this.router.navigate(['/carregamento']);
          this.deleteStore(id)
        } else {
          swal('Cancelado', `a loja: ${id} no ser deletado`)
        }
      })
  }

  deleteCashierSwal(id) {
    swal({
      title: 'Tem certeza?',
      text: `Gostaria de deletar o caixa: ${id}`,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Sim, deletar!'
    })
      .then((result) => {
        if (result.value) {
          this.router.navigate(['/carregamento']);
          this.deleteCashier(id)
        } else {
          swal('Cancelado', `o caixa: ${id} no ser deletado`)
        }
      })
  }

  deleteDiscountSwal(id) {
    swal({
      title: 'Tem certeza?',
      text: `Gostaria de deletar o desonto: ${id}`,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Sim, deletar!'
    })
      .then((result) => {
        if (result.value) {
          this.router.navigate(['/carregamento']);
          this.deleteDiscount(id)
        } else {
          swal('Cancelado', `o desonto: ${id} no ser deletado`)
        }
      })
  }

  deleteUserSwal(id, name) {
    swal({
      title: 'Tem certeza?',
      text: `Gostaria de deletar o usurio: ${name}`,
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Sim, deletar!'
    })
      .then((result) => {
        if (result.value) {
          this.router.navigate(['/carregamento']);
          this.deleteUser(id)
        } else {
          swal('Cancelado', `o usurio: ${name} no ser deletado`)
        }
      })
  }

  deleteContract(id) {
    this.contractService.deleteContract(id).subscribe(res => {
      if (res.sucesso) {
        this.success(res.mensagem, this.router.navigate(['/contratos/listar']))
      } else {
        this.error(res.mensagem)
      }
    })
  }

  deleteStore(id) {
    this.storeService.deleteStore(id).subscribe(res => {
      if (res.sucesso) {
        this.success(res.mensagem, this.router.navigate(['/lojas/listar']))
      } else {
        this.error(res.mensagem)
      }
    })
  }

  deleteCashier(id) {
    this.cashierService.deleteCashier(id).subscribe(res => {
      if (res.sucesso) {
        this.success(res.mensagem, this.router.navigate(['/caixas/listar']))
      } else {
        this.error(res.mensagem)
      }
    })
  }
 
  deleteDiscount(id) {
    this.descontoService.deleteDescount(id).subscribe(res => {
      if (res.sucesso) {
        this.success(res.mensagem, this.router.navigate(['/descontos/listar']))
      } else {
        this.error(res.mensagem)
      }
    })
  }
  
  deleteUser(id) {
    this.userService.deleteUser(id).subscribe(res => {
      if (res.sucesso) {
        this.success(res.mensagem, this.router.navigate(['/usuarios/listar']))
      } else {
        this.error(res.mensagem)
      }
    })
  }
}
