import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ContractsService, StoresService } from 'src/app/services/http-requests';
import { DescontosService } from 'src/app/services/http-requests/descontos/descontos.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-discount-configurations',
  templateUrl: './discount-configurations.component.html',
  styleUrls: ['./discount-configurations.component.scss']
})
export class DiscountConfigurationsComponent implements OnInit {

  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  percentage
  absolut

  percentagediscountValue
  absolutdiscountValue

  contracts
  storesList
  companyId

  selectedContractId


  dropdownList = [];
  selectedItems = [];
  dropdownSettings = {};

  storesToPost: any[] = []


  constructor(
    private contractsService: ContractsService,
    private storesService: StoresService,
    private discountService: DescontosService,
    private router: Router,
    private swalSwervice: SwalService,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if (permissionsArr.includes('configuracoes')) {
          this.canView = true
          this.router.navigateByUrl('/configuracoes')
          this.getcontracts()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  prepareStoresMultiSelect(evt) {
    this.dropdownSettings = {
      singleSelection: false,
      idField: 'store_id',
      textField: 'store_name',
      selectAllText: 'Select All',
      unSelectAllText: 'UnSelect All',
      itemsShowLimit: 3,
      allowSearchFilter: true
    };
    let preparationArr: any[] = []
    for (let i = 0; i < evt.length; i++) {
      preparationArr.push(
        { store_id: evt[i].id_loja, store_name: evt[i].nome_loja }
      )
    }
    this.dropdownList = preparationArr
  }

  savePercentage(evt) {
    this.percentagediscountValue = evt
  }

  saveAbsolut(evt) {
    this.absolutdiscountValue = evt
  }

  getcontracts() {
    this.contractsService.getAllContractsInfo().subscribe(res => {
      this.contracts = res.data.contracts
    })
  }

  saveSelectedContract(contractId) {
    this.selectedContractId = contractId
    this.contractsService.getOneContract(contractId).subscribe(res => {
      this.companyId = res.data.id_empresa
    })
    this.storesService.getContractsList(contractId).subscribe(res => {
      this.storesList = res.data
      this.prepareStoresMultiSelect(res.data)
    })
  }

  createConfig() {
    if (this.storesToPost.length == this.storesList.length) {
      let body =
      {
        "valor_maximo_desconto_absoluto": this.absolutdiscountValue,
        "valor_maximo_desconto_porcentagem": this.percentagediscountValue,
      }
      this.discountService.createDisconfigByContract(this.companyId, body).subscribe(res => {
        if (res.sucesso) {
          this.storesToPost = [];
          this.swalSwervice.success(res.mensagem, this.router.navigateByUrl('/dashboard'))
        } else {
          this.swalSwervice.error(res.mensagem)
        }
      })
    } else {
      for (let i = 0; i < this.storesToPost.length; i++) {
        let body =
        {
          "id_store": this.storesToPost[i].store_id,
          "valor_maximo_desconto_absoluto": this.absolutdiscountValue,
          "valor_maximo_desconto_porcentagem": this.percentagediscountValue,
        }
        this.discountService.createDisconfigByStore(body).subscribe(res => {
          if (res.sucesso) {
            this.storesToPost = [];
            this.swalSwervice.success(res.mensagem, this.router.navigateByUrl('/dashboard'))
          } else {
            this.swalSwervice.error(res.mensagem)
          }
        })
      }
    }
  }

  onItemSelect(item: any) {
    this.storesToPost.push({ store_id: item.store_id })
  }

  onSelectAll(items: any) {
    for (let i = 0; i < items.length; i++) {
      this.storesToPost.push({ store_id: items[i].store_id })
    }
  }

}
