import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ContractsService, StoresService } from 'src/app/services/http-requests';
import { DescontosService } from 'src/app/services/http-requests/descontos/descontos.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-desconto-create',
  templateUrl: './desconto-create.component.html',
  styleUrls: ['./desconto-create.component.scss']
})
export class DescontoCreateComponent implements OnInit {
  @Output() createPage = new EventEmitter;
  @Input() contractsIds;

  @Input() selectedContractId
  @Input() selectedStoreId
  @Input() selectedDiscountId

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  configPercentMaxValue
  configAbsolutMaxValue
  configType


  storesList;
  receivedDiscount = {
    discountName: '',
    discountId: '',
    discountType: '',
    discountValue: '',
    discountInitDate: '',
    discountEndDate: ''
  }

  discountForm: FormGroup;
  directivesForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private contractService: ContractsService,
    private storeService: StoresService,
    private descontoService: DescontosService,
    private swalService: SwalService
  ) { }

  ngOnInit() {
    if (this.selectedDiscountId) {
      this.getDiscountToEdit()
      this.getDiscountConfig()
    } else {
      this.getDiscountConfig()
    }

    this.directivesForm = this.fb.group({
      storeInput: ['', [Validators.required]],
      contractInput: ['', [Validators.required]]
    })

    this.discountForm = this.fb.group({
      discountName: ['', [Validators.required]],
      discountType: ['', [Validators.required]],
      discountValue: [''],
      discountPercentValue: ['', [Validators.max(100), Validators.maxLength(3)]],
      initDate: ['', [Validators.required]],
      endDate: ['', [Validators.required]],
    });

    this.discountForm.valueChanges.subscribe();
    this.directivesForm.valueChanges.subscribe();
  }

  getDiscountConfig() {
    this.descontoService.listDisconfigByStore(this.selectedStoreId).subscribe(res => {
      this.receivedDiscount.discountType = res.data.tipo_desconto
      this.configPercentMaxValue = res.data.valor_maximo_desconto_porcentagem
      this.configAbsolutMaxValue = res.data.valor_maximo_desconto_absoluto
      this.discountForm.get('discountType').setValue(JSON.stringify(res.data.tipo_desconto));
    })
  }

  getDiscountToEdit() {

    this.descontoService.getDiscountById(this.selectedStoreId, this.selectedDiscountId).subscribe(res => {
      this.receivedDiscount.discountName = res.data.nome
      this.receivedDiscount.discountId = res.data.idDesconto
      this.receivedDiscount.discountType = res.data.tipo
      this.receivedDiscount.discountValue = res.data.valor
      this.receivedDiscount.discountInitDate = res.data.vigInicial
      this.receivedDiscount.discountEndDate = res.data.vigFinal
    }
    );
  }

  saveBtn() {
    let discountId = this.selectedDiscountId;
    let value;
    let storeId

    if (this.selectedStoreId) {
      storeId = this.selectedStoreId
    } else if (this.directivesForm.get('storeInput').value) {
      storeId = this.directivesForm.get('storeInput').value
    }

    if (this.discountForm.get('discountPercentValue').value) {
      value = this.discountForm.get('discountPercentValue').value
    } else if (this.discountForm.get('discountValue').value) {
      value = this.discountForm.get('discountValue').value
    }
    let postBody = [
      {
        "idDesconto": discountId,
        "idLoja": storeId,
        "idUsuario": this.user.userId,
        "nome": this.discountForm.get('discountName').value,
        "status": 2,
        "tipo": this.discountForm.get('discountType').value,
        "valor": value,
        "vigInicial": this.discountForm.get('initDate').value,
        "vigFinal": this.discountForm.get('endDate').value,
        "dataCadastro": new Date()
      }
    ]
    if (!discountId) {
      this.descontoService.createDiscount(postBody).subscribe(res => {
        if (res.sucesso == false) {
          this.swalService.error(res.mensagem)
        } else {
          this.swalService.success(res.mensagem, this.changePage())
        }
      })
    } else {
      this.descontoService.updateDiscount(discountId, postBody).subscribe(res => {
        if (res.sucesso == false) {
          this.swalService.error(res.mensagem)
        } else {
          this.swalService.success(res.mensagem, this.changePage())
        }
      })
    }
  }

  getStoresList(evt) {
    this.storeService.getStoresFilteredWithContract(evt).subscribe(res => {
      this.storesList = res.data;
    })
  }

  get nameValue() {
    return this.discountForm.get('discountName');
  }

  get typeValue() {
    return this.discountForm.get('discountType');
  }

  get percentageValue() {
    return this.discountForm.get('discountPercentValue');
  }

  get intValue() {
    return this.discountForm.get('discountValue');
  }

  get initDateValue() {
    return this.discountForm.get('initDate');
  }

  get endDateValue() {
    return this.discountForm.get('endDate');
  }

  get contractInputValue() {
    return this.directivesForm.get('contractInput');
  }

  getStoreInputValue() {
    return this.directivesForm.get('storeInput')
  }

  changePage() {
    this.createPage.emit(true)
  }

}


