import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http/http.service';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2'
import { API } from 'src/app/services/config';
import { ContractsService, StoresService } from 'src/app/services/http-requests';
import swal from 'sweetalert2';
import { SwalService } from 'src/app/services/swal/swal.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'app-loja-listar',
  templateUrl: './loja-listar.component.html',
  styleUrls: ['./loja-listar.component.scss']
})
export class LojaListarComponent implements OnInit {
  selectedTokPagId
  page
  contractsList
  storesList
  selectedContract
  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  totalItems = null;
  timeout = null;
  filterLoaded = false;
  storeSelected;
  modalDeleteOpened = false
  skip = 0;
  take = 10;
  contractsIds: any;
  lastContractId

  filterInput = {
    contractId: 'Identificao do Contrato',
    socialReason: ''
  }

  storesTitle = [
    'Id. Loja',
    'Nome da Loja',
    'Situao Loja',
    'CNPJ',
    'Qtde. Caixa'
  ]

  stores = []
  storesTest = {
    storeId: '100.200',
    storeName: 'Primeira Loja',
    storeSituation: 'Criada',
    CNPJ: '20.333/444/0020-10',
    cashierQtd: 10,
  }
  p: number;

  constructor(
    private httpService: HttpService,
    private route: ActivatedRoute,
    private contractService: ContractsService,
    private storeService: StoresService,
    private swalService: SwalService,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for(let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if(permissionsArr.includes('lojas')) {
          localStorage.setItem('currentUri', 'lojas/listar')
          this.canView = true
          this.router.navigateByUrl('/lojas')
          this.initFuncs()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  initFuncs() {
    this.getContractList()
    this.route.params.subscribe((params) => {
      this.filterInput.contractId = params.idContract
      if (this.filterInput.contractId)
        this.filterLoaded = true
    })
    if (this.filterLoaded) {
      this.storeService.getStoresFilteredWithContract(this.filterInput.contractId).subscribe(res => {
        this.storesList = res.data;
        this.filterLoaded = true
      })
    }
  }

  getContractList() {
    this.contractService.getAllContractsInfo().subscribe(res => {
      this.contractsList = res.data.contracts
    })
  }

  listStores(evt) {
    this.selectedTokPagId = evt
    this.selectedContract = evt
    this.storeService.getStoresFilteredWithContract(evt).subscribe(res => {
      this.storesList = res.data;
      this.filterLoaded = true
    })
  }

  deleteStore(evt) {
    this.swalService.deleteStoreSwal(evt)
  }

  selectStore(storeId) {
    this.storeSelected = storeId;
  }

  getStatusToFilter(evt) {
    this.storeService.getStoresFilteredWithContract(this.selectedContract).subscribe(res => {
      if (evt === '2' || evt === '0') {
        let parsedStatus = parseInt(evt)
        let returned: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          if (parsedStatus == res.data[i].status_loja) {
            returned.push(res.data[i])
          }
        }
        this.storesList = returned;
        this.filterLoaded = true
      } else {
        this.storesList = res.data;
        this.filterLoaded = true
      }
    })
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.storeService.getStoresFilteredWithContract(this.selectedContract).subscribe(res => {
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].nome_loja.includes(evt)) {
            returned.push(res.data[i])
          }
        }
      })
      this.storesList = returned;
      this.filterLoaded = true
    })
  }

}