import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpService } from 'src/app/services/http/http.service';
import Swal from 'sweetalert2'
import { API } from 'src/app/services/config';
import { StoresService, ContractsService } from 'src/app/services/http-requests';
import swal from 'sweetalert2';
import { SwalService } from 'src/app/services/swal/swal.service';
import { DescontosService } from 'src/app/services/http-requests/descontos/descontos.service';

@Component({
  selector: 'app-loja-cadastro-edit',
  templateUrl: './loja-cadastro-edit.component.html',
  styleUrls: ['./loja-cadastro-edit.component.scss']
})
export class LojaCadastroEditComponent implements OnInit {
  isEditing = false

  contract = {
    contractName: '',
    contractId: ''
  }
  selectedStore
  selectedStoreId
  inputStatus
  storeStatus
  statusToSend = 2
  private isCreate: boolean; //create or edit?
  private validCNPJ = false;
  private previousRoute: string;
  private guid;
  private contractId;
  private socialReason;
  private guid_empresa;
  private idStore;
  store: FormGroup;
  private isSaving: boolean = false;
  private filterInput = {
    contractId: 'Identificao do Contrato'
  }
  private contractsIds: any;
  private lastContractId: string;
  private qtdPassadas: number = 0

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private httpService: HttpService,
    private storeService: StoresService,
    private contractService: ContractsService,
    private swalService: SwalService,
    private configService: DescontosService
  ) {
    this.store = fb.group({
      'idStore': '',
      'CNPJ': [null, Validators.required],
      'name': [null, Validators.required],
      'situation': [false, Validators.required],
      'cep': [null, Validators.required],
      'estado': [null, Validators.required],
      'cidade': [null, Validators.required],
      'bairro': [null, Validators.required],
      'logradouro': [null, Validators.required],
      'complemento': [''],
      'numero': [null, Validators.required],
    })
  }

  ngOnInit() {
    localStorage.setItem('currentUri', 'lojas/listar')
    this.isCreate = this.router.url.split('/')[2] == 'cadastro' ? true : false

    if (this.isCreate) {
      this.store.controls['situation'].disable()

      this.route.params.subscribe(async params => {

        let dependence = JSON.parse(params.dependence)
        this.contractId = dependence.tokPag
        this.filterInput.contractId = this.contractId
        this.getContract(this.filterInput.contractId)
        let res = await this.httpService.get(`${API}/api/contract/${this.contractId}`)
        if (res == 'error') {
          this.swalService.error(res.mensagem)
        }


        this.contractsIds = await this.httpService.get(`${API}/api/store/getStoresContractsIds/` + localStorage.getItem('idusuario'));
        if (this.contractsIds == 'error') {
          this.swalService.error(res.mensagem)
        }
      })
    } else {
      this.store.controls['CNPJ'].disable()
      this.route.params.subscribe(async params => {
        let dependence = JSON.parse(params.dependence)
        this.idStore = dependence.idStore;
        this.contractId = dependence.tokPag;
        this.socialReason = dependence.socialReason

        this.filterInput.contractId = this.contractId
        this.getContract(this.filterInput.contractId)

        this.store.controls['idStore'].setValue(this.idStore);

        this.storeService.gatOneStore(this.idStore).subscribe(res => {
          if (res.sucesso) {
            this.selectedStore = res.data
            this.selectedStoreId = res.data.id_loja
            this.fillFields(res.data)
            this.statusChange(res.data.status_loja)
            this.isEditing = true
          } else {
            this.swalService.error(res.mensagem)
          }
        })
      })
    }
  }

  getContract(id) {
    this.contractService.getOneContract(id).subscribe(res => {
      this.contract.contractId = res.data.id_empresa
      this.contract.contractName = res.data.razao_social
    })
  }

  statusChange(evt) {
    if (evt == 2) {
      this.inputStatus = true
      this.statusToSend = 2
    } else if (evt == 0) {
      this.inputStatus = false
      this.statusToSend = 0
    } else {
      this.storeStatus = evt.target.checked
      if (this.storeStatus) {
        this.inputStatus = true
        this.statusToSend = 2
      } else if (!this.storeStatus) {
        this.inputStatus = false
        this.statusToSend = 0
      }
    }
    return this.statusToSend
  }

  btnSalvar() {
    let storeToSend = {
      "contrato_tokpag": this.filterInput.contractId,
      "id_empresa": this.contract.contractId,
      "razao_social": this.contract.contractName,
      "cnpj": this.store.get('CNPJ').value,
      "nome_loja": this.store.get('name').value,
      "status_loja": this.statusToSend,
      "endereco": {
        "logradouro": this.store.get('logradouro').value,
        "numero": this.store.get('numero').value,
        "complemento": this.store.get('complemento').value,
        "bairro": this.store.get('bairro').value,
        "cidade": this.store.get('cidade').value,
        "estado": this.store.get('estado').value,
        "cep": this.store.get('cep').value
      }
    }

    if (this.selectedStoreId) {
      this.storeService.updateStore(this.selectedStoreId, storeToSend).subscribe(res => {
        if (res.sucesso) {
          this.swalService.success(res.mensagem, this.router.navigate(['/lojas/listar', this.contractId]))
        } else {
          this.swalService.error(res.mensagem)
        }
      })
    } else {
      this.storeService.createStore(storeToSend).subscribe(res => {
        if (res.sucesso) {
          this.swalService.success(res.mensagem, this.router.navigate(['/lojas/listar', this.contractId]))
          let body =
          {
            "id_store": res.data.id_loja,
            "valor_maximo_desconto": 100000000,
            "tipo_desconto": 0
          }
          this.configService.createDisconfigByStore(body).subscribe(res => {
          })
        } else {
          this.swalService.error(res.mensagem)
        }
      })
    }
  }

  fillFields(res) {
    this.store.controls['CNPJ'].setValue(res.cnpj)
    this.store.controls['name'].setValue(res.nome_loja)
    this.store.controls['situation'].setValue(res.status_loja == "Ativo" ? true : false)
    this.store.controls['logradouro'].setValue(res.endereco.logradouro)
    this.store.controls['numero'].setValue(res.endereco.numero)
    this.store.controls['complemento'].setValue(res.endereco.complemento)
    this.store.controls['bairro'].setValue(res.endereco.bairro)
    this.store.controls['cidade'].setValue(res.endereco.cidade)
    this.store.controls['estado'].setValue(res.endereco.estado)
    this.store.controls['cep'].setValue(res.endereco.cep)
    this.validCNPJ = true
  }

  async getCep() {
    this.qtdPassadas++
    if (this.store.controls['cep'].value.length === 8 && (this.isCreate || this.qtdPassadas > 2)) {

      let res = await this.httpService.get(`https://lx-smb-api-qa.azurewebsites.net/api/v1/cep/${this.store.controls['cep'].value}`)
      this.store.controls['bairro'].setValue(res.value.BAIRRO)
      this.store.controls['cidade'].setValue(res.value.MUNICIPIO)
      this.store.controls['estado'].setValue(res.value.UF)

      if (res.value.OBS) {
        this.store.controls['logradouro'].setValue(res.value.OBS)
      }
      else {
        this.store.controls['logradouro'].setValue(res.value.LOGRADOURO)
      }
    }
  }


  async OLDbtnSalvar() {
    if (!this.validarCNPJ())
      return

    let storeRequest: any = {
      cnpj: this.store.controls['CNPJ'].value,
      nome_loja: this.store.controls['name'].value,
      contrato_tokpag: this.filterInput.contractId,
      status_loja: this.store.controls['situation'].value ? 'Ativo' : 'Inativo',
      endereco: {
        logradouro: this.store.controls['logradouro'].value,
        numero: this.store.controls['numero'].value,
        complemento: this.store.controls['complemento'].value,
        bairro: this.store.controls['bairro'].value,
        cidade: this.store.controls['cidade'].value,
        estado: this.store.controls['estado'].value,
        cep: this.store.controls['cep'].value
      }

    }

    if (this.isSaving) {
      return
    }

    this.isSaving = true

    let res

    if (this.isCreate) {
      try {
        res = await this.httpService.post('${API}/api/store', storeRequest)
        if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        }
      } catch (error) {
        this.isSaving = false
        throw error
      }
    } else {

      try {
        storeRequest.id_loja = this.idStore;
        res = await this.httpService.put(`${API}/api/store/${this.idStore}`, storeRequest)

        if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        }

      } catch (error) {
        this.isSaving = false

        throw error;
      }
    }

    setTimeout(() => {
      this.isSaving = false
    }, 2000);

    if (res.sucesso || res.status == 200 || res.status == 201) {
      this.swalService.success(res.mensagem, this.router.navigate(['/lojas/listar', this.contractId]))
    }

  }


  validarCNPJ() {
    let cnpj: string = this.store.controls['CNPJ'].value;

    if (cnpj == '') {
      this.validCNPJ = false;
      return false
    }

    if (cnpj.length != 14) {
      this.validCNPJ = false;
      return false
    }

    if (cnpj == "00000000000000" ||
      cnpj == "11111111111111" ||
      cnpj == "22222222222222" ||
      cnpj == "33333333333333" ||
      cnpj == "44444444444444" ||
      cnpj == "55555555555555" ||
      cnpj == "66666666666666" ||
      cnpj == "77777777777777" ||
      cnpj == "88888888888888" ||
      cnpj == "99999999999999") {
      this.validCNPJ = false;
      return false
    }

    let tamanho = cnpj.length - 2
    let numeros: any = cnpj.substring(0, tamanho);
    let digitos = cnpj.substring(tamanho);
    let soma = 0;
    let pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
        pos = 9;
    }
    let resultado: any = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(0)) {
      this.validCNPJ = false;
      return false
    }

    tamanho = tamanho + 1;
    numeros = cnpj.substring(0, tamanho);
    soma = 0;
    pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
        pos = 9;
    }
    resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(1)) {
      this.validCNPJ = false;
      return false
    }

    this.validCNPJ = true;
    return true

  }

}