import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { StoresService } from 'src/app/services/http-requests';
import { DescontosService } from 'src/app/services/http-requests/descontos/descontos.service';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-desconto-list',
  templateUrl: './desconto-list.component.html',
  styleUrls: ['./desconto-list.component.scss']
})
export class DescontoListComponent implements OnInit {

  @Output() createDiscount = new EventEmitter
  @Output() emitStoreId = new EventEmitter;
  @Output() emitDiscountId = new EventEmitter;
  @Output() emitContractId = new EventEmitter;

  @Input() contractIdList;
  storeList
  discountsList
  page
  shwoList = false;
  enableBtn = false

  selectedStoreId

  cashiers = []
  totalItems = null;
  timeout = null;
  modalQrOpened = false;
  actualImage;
  skip = 0;
  readonly take = 10;

  modalDeleteOpened = false;
  modalCadastroDesconto = false;
  qrCodeSelected;
  descriptionSelected;
  cashierSelected: string;
  filterLoaded: boolean;
  linePush: boolean;
  isContractLoaded: boolean = false
  contractsIds = undefined
  storesIds = undefined
  storeValue
  objectKeys = Object.keys
  p: number;
  cadDesconto;
  desconto_lista: any = []
  desconto_cad: any = []
  dadosCadDesconto: any = []
  idLoja;
  tipoDesconto
  valorDesconto
  vigInicialDesconto
  vigFinalDesconto
  today;
  today_1;
  maxDayInicial;
  errovalordesconto;
  maskValor;
  maskValorplaceholder;
  maskValorPrefix;
  filterInput = {
    code: '',
    storeId: 'Identificao da Loja',
    contractId: 'Identificao do Contrato'
  }
  lastStoreId
  lastContractId
  lastCode

  cashiersTitle =
    [
      'Tipo Desconto',
      'Valor Desconto',
      'Vigncia Inicial',
      'Vigncia Final',
      'Usurio',
      'Data'
    ]

  cashierTest = {
    cashierId: '100-10',
    cashierDescription: 'Primeiro Caixa',
    cashierSituation: 'Criado',
    cashierHash: 'Hash do Caixa',
    status: 'Inativo',
  }

  constructor(
    private router: Router,
    private storesService: StoresService,
    private descontoService: DescontosService,
    private swalService: SwalService
  ) {
    var d = new Date(),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      day_1 = '' + (d.getDate() + 1),
      year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    this.today = year + '-' + month + '-' + day;
    this.maxDayInicial = '';

    this.today_1 = year + '-' + month + '-' + day_1;
  }

  setmaxday() {
    this.maxDayInicial = this.vigFinalDesconto;
  }
  setmindayFinal() {
    this.today_1 = this.vigInicialDesconto;
  }

  ngOnInit() {
  }

  listStores(contractId) {
    this.storesService.getStoresFilteredWithContract(contractId).subscribe(res => {
      this.storeList = res.data;
    })
  }

  getDiscount(evt) {
    this.selectedStoreId = evt
    this.enableBtn = true
    this.descontoService.getDescount(evt).subscribe(res => {
      this.discountsList = res.data
      this.shwoList = true
    })
  }

  maskValorTipo(tipo) {
    if (tipo == 0) {
      this.maskValor = '000,00';
      this.maskValorplaceholder = '000,00';
      this.maskValorPrefix = 'R$';
    } else if (tipo == 1) {
      this.maskValor = '00,00';
      this.maskValorplaceholder = '00,00';
      this.maskValorPrefix = '%';
    }

  }

  deleteDiscount(discountId) {
    this.swalService.deleteDiscountSwal(discountId)
  }

  sendDelete(id) {
    this.descontoService.deleteDescount(id).subscribe(res => {
      if (res.sucesso) {
        this.swalService.success(res.mensagem, this.router.navigate(['/descontos/listar']))
      } else {
        this.swalService.error(res.mensagem)
      }
    })
  }

  goToCreatePage(contractId, storeId) {
    this.emitContractId.emit(contractId)
    this.emitStoreId.emit(storeId)
    this.createDiscount.emit(false)
  }

  goToEdit(contractId, storeId, discountId) {
    this.emitContractId.emit(contractId)
    this.emitStoreId.emit(storeId)
    this.emitDiscountId.emit(discountId);
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.descontoService.getDescount(this.selectedStoreId).subscribe(res => {
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].nome.includes(evt)) {
            returned.push(res.data[i])
          }
        }
      })
      this.discountsList = returned
      this.shwoList = true
      this.filterLoaded = true
    }, 500)
  }

}


