import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { AuthService } from 'src/app/services/auth/auth.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'topbar',
  templateUrl: './topbar.component.html',
  styleUrls: ['./topbar.component.scss']
})
export class TopbarComponent implements OnInit {
  canView = false
  pathToShow
  pathNames

  pageName

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  routes = [];

  constructor(
    private router: Router,
    private authService: AuthService,
    private userService: UserService
  ) {

    router.events.subscribe((event) => {
      if (event instanceof NavigationStart) {
        this.modifyingHeaderRoute(event.url)
      }
    });
  }

  ngOnInit() {
    this.modifyingHeaderRoute(this.router.url)
    this.getUserPermission()
  }

  getUserPermission() {
    this.userService.getPermissions(this.user.userId).subscribe(res => {
      this.pageName = res.data[0].nome
      let permissionsArr: any[] = []
      let names: any[] = []
      for (let i = 0; i < res.data.length; i++) {
        permissionsArr.push(res.data[i].path)
        names.push(res.data[i].nome)
      }
      this.pathToShow = permissionsArr
      this.pathNames = names
      if (permissionsArr.includes('configuracoes')) {
        this.canView = true
      }
    })
  }

  modifyingHeaderRoute(url) {
    this.routes = url.split('/')
    this.routes.splice(0, 1) // tira o '/'
    if (this.pathToShow) {
      for (let i = 0; i < this.pathToShow.length; i++) {
        if (this.routes[1] === 'editar') {
          if (this.pathToShow[i] === this.routes[0]) {
            this.pageName = `Editar ${this.pathNames[i]}`
          }
        } else if (this.routes[1] === 'cadastro') {
          if (this.pathToShow[i] === this.routes[0]) {
            this.pageName = `Cadastrar ${this.pathNames[i]}`
          }
        } else {
          if (this.pathToShow[i] === this.routes[0]) {
            this.pageName = this.pathNames[i]
          }
        }
      }
    }
  }

  logout() {
    this.router.navigateByUrl('/login');
    localStorage.clear()
  }

}
