import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';
import { CashiersService, ContractsService, DashboardService, StoresService } from 'src/app/services/http-requests';
import { ChartType } from 'chart.js';
import { MultiDataSet, Label } from 'ng2-charts';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/http-requests/user/user.service';
import swal from 'sweetalert2';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  dhowDateInput = false
  showToDate = false
  colors = [
    '#78cee8',
    '#c1454e',
    '#5e29e9',
    '#c809ca',
    '#f40404',
    '#23743b',
    '#72489f',
    '#4ccdc6',
    '#3d5237',
    '#d5c87d',
    '#07381b',
    '#9d851c',
    '#380055',
    '#16866e',
    '#91e01c',
    '#2d07f9',
    '#c72a27',
    '#a4ca72',
    '#450c41',
    '#3295ce',
    '#82345f',
    '#ac1f14',
    '#b06822',
    '#b0337d',
    '#1170cd',
    '#6c215f',
    '#0461f3',
    '#349464',
    '#412747',
    '#7fa540',
    '#4ec00e',
    '#77be80',
    '#1c7668',
    '#f59e27',
    '#1d42a9'
  ]

  loaded = false
  canView = false

  resultsSum = {
    contracts: '',
    stores: '',
    cashiers: ''
  }

  jsonContracts
  jsonStores
  jsonCadhies
  contractLineLength
  storeLineLength
  cashierLineLength
  contractsColor = []
  storesColor = []
  cashierColor = []
  businessId

  doughnutChartLabels: Label[] = []
  doughnutChartData: MultiDataSet = []
  doughnutChartType: ChartType = 'doughnut'

  barChartOptions = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  barChartLabels: Label = [];
  barChartType: ChartType = 'bar';
  barChartLegend = false;
  barChartData: MultiDataSet = [];

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }
  results;
  contracts
  selectedcontract
  stores
  selectedstore
  cashiers
  selectedcashier

  haveResults = false

  selectedFilter = null
  selectedDate;
  selectedFromDate;
  selectedToDate


  today = new Date();


  constructor(
    private dashService: DashboardService,
    private contractsService: ContractsService,
    private storeService: StoresService,
    private cashierService: CashiersService,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if (permissionsArr.includes('dashboard')) {
          localStorage.setItem('currentUri', 'dashboard')
          this.canView = true
          this.router.navigateByUrl('/dashboard')
          this.getResults();
          this.getActiveSum()
          this.getContractList()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  getActiveSum() {
    this.dashService.getActiveSuum().subscribe(res => {
      this.resultsSum.contracts = res.data.active_contratos_quantity
      this.resultsSum.stores = res.data.active_store_quantity
      this.resultsSum.cashiers = res.data.active_cashier_quantity
      this.loaded = true
    })
  }

  getContractsPercentage(value) {
    let math
    math = 100 / value
    this.contractLineLength = `${math}%`
  }

  getStoresPercentage(value) {
    let math
    math = 100 / value
    this.storeLineLength = `${math}%`
  }

  getCashierPercentage(value) {
    let math
    math = 100 / value
    this.cashierLineLength = `${math}%`
  }

  getContractList() {
    this.contractsService.getByUserId().subscribe(res => {
      this.jsonContracts = res.data
      this.contracts = res.data

      for (let i = 0; i < res.data.length; i++) {
        this.contractsColor[i] = this.colors[Math.floor(Math.random() * this.colors.length)]
      }
    })
  }

  listStores(contractId) {
    this.storeService.getStoresFilteredWithContract(contractId).subscribe(res => {
      this.stores = res.data;
      this.jsonStores = res.data

      for (let i = 0; i < res.data.length; i++) {
        this.storesColor[i] = this.colors[Math.floor(Math.random() * this.colors.length)]
      }
    })
  }

  getCashiers(storeId) {
    this.cashierService.getCashiersList(storeId).subscribe(res => {
      this.cashiers = res.data.cashier
      this.jsonCadhies = res.data.cashier

      for (let i = 0; i < res.data.cashier.length; i++) {
        this.cashierColor[i] = this.colors[Math.floor(Math.random() * this.colors.length)]
      }
    })
  }

  mountDonoutChart(values) {
    let getNames: any[] = []
    let getValues: any[] = []
    for (let i = 0; i < values.length; i++) {
      getNames.push(values[i].nome)
      getValues.push(values[i].valor)
    }

    this.barChartLabels = [
      getNames[0],
      getNames[1],
      getNames[2],
    ]
    this.doughnutChartData = [
      getValues[0],
      getValues[1],
      getValues[2],
    ]
  }

  mountBarChart(values) {
    let getNames: any[] = []
    let getValues: any[] = []
    for (let i = 0; i < values.length; i++) {
      getNames.push(values[i].nome)
      getValues.push(values[i].valor)
    }

    this.doughnutChartLabels = [
      getNames[0],
      getNames[1],
      getNames[2],
    ]
    this.barChartData = [
      getValues[0],
      getValues[1],
      getValues[2],
    ]
  }

  getResults() {

    this.dashService.getCountResults('', '').subscribe(response => {
      this.mountDonoutChart(response.data.nomes_e_valores)
      this.haveResults = true
    });

    this.dashService.getSumResults('', '').subscribe(res => {
      this.mountBarChart(res.data.nomes_e_valores)
      this.haveResults = true
    })
  }


  saveSelectedContract(evt) {
    this.selectedcontract = evt
    this.listStores(evt)
  }

  saveSelectedStore(evt) {
    this.selectedstore = evt
    this.getCashiers(evt)
  }

  saveSelectedCashier(evt) {
    this.selectedcashier = evt
  }

  setDays(evt) {
    this.selectedFilter = evt
    let relativeDate
    let formatedDate
    let today = moment().format('YYYY-MM-DD')

    if (evt != 5) {
      this.dhowDateInput = false
      relativeDate = moment().subtract(evt, "days");
      formatedDate = moment(relativeDate).format('YYYY-MM-DD')
      this.selectedDate = formatedDate

      this.dashService.getCountResults(this.selectedDate, today).subscribe(res => {
        if (res.sucesso) {
          if (res.data.cancelados == 0 && res.data.concluidos == 0 && res.data.excluidos == 0) {
            swal({
              text: 'No foi encontrado nenhum resultado para essa pesquisa!',
              type: 'info',
              showCloseButton: true,
              confirmButtonText: 'Ok'
            })
            this.haveResults = false
          }
          this.mountDonoutChart(res.data.nomes_e_valores)
          this.haveResults = true
        } else {
          swal({
            text: res.mensagem,
            type: 'info',
            showConfirmButton: true,
            confirmButtonText: 'Ok'
          })
          this.haveResults = false
        }
      })

      this.dashService.getSumResults(this.selectedDate, today).subscribe(res => {
        if (res.sucesso) {
          if (res.data.cancelados == 0 && res.data.concluidos == 0 && res.data.excluidos == 0) {
            swal({
              text: 'No foi encontrado nenhum resultado para essa pesquisa!',
              type: 'info',
              showCloseButton: true,
              confirmButtonText: 'Ok'
            })
            this.haveResults = false
          }
          this.mountBarChart(res.data.nomes_e_valores)
          this.haveResults = true
        } else {
          swal({
            text: res.mensagem,
            type: 'info',
            showConfirmButton: true,
            confirmButtonText: 'Ok'
          })
          this.haveResults = false
        }
      })

    }

  }

  getFromDate(evt) {
    this.setDays(5)
    this.selectedFromDate = evt
    this.showToDate = true
  }

  getToDate(evt) {
    this.setDays(5)
    this.selectedToDate = evt
    this.filterDateRage()
  }

  filterDateRage() {
    this.dashService.getCountResults(this.selectedFromDate, this.selectedToDate).subscribe(res => {
      if (res.sucesso) {
        if (res.data.cancelados == 0 && res.data.concluidos == 0 && res.data.excluidos == 0) {
          swal({
            text: 'No foi encontrado nenhum resultado para essa pesquisa!',
            type: 'info',
            showCloseButton: true,
            confirmButtonText: 'Ok'
          })
          this.haveResults = false
        }
        this.mountDonoutChart(res.data.nomes_e_valores)
        this.haveResults = true
      } else {
        swal({
          text: res.mensagem,
          type: 'info',
          showConfirmButton: true,
          confirmButtonText: 'Ok'
        })
        this.haveResults = false
      }
    })

    this.dashService.getSumResults(this.selectedFromDate, this.selectedToDate).subscribe(res => {
      if (res.sucesso) {
        if (res.data.cancelados == 0 && res.data.concluidos == 0 && res.data.excluidos == 0) {
          swal({
            text: 'No foi encontrado nenhum resultado para essa pesquisa!',
            type: 'info',
            showCloseButton: true,
            confirmButtonText: 'Ok'
          })
          this.haveResults = false
        }
        this.mountBarChart(res.data.nomes_e_valores)
        this.haveResults = true
      } else {
        swal({
          text: res.mensagem,
          type: 'info',
          showConfirmButton: true,
          confirmButtonText: 'Ok'
        })
        this.haveResults = false
      }
    })
  }

}
