import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { RequestOptions } from '@angular/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';

@Injectable({
  providedIn: 'root'
})
export class ContractsService {
  options: RequestOptions;
  contractsBaseUrl = API + '/api/contract'

  constructor(private http: HttpClient) { }

  getEnabledContract(contractId, cnpj) {
    return this.http.get<any>(`${this.contractsBaseUrl}/GetContractData/${contractId}/cliente/${cnpj}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getAllContractsInfo() {
    return this.http.get<any>(`${this.contractsBaseUrl}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getOneContract(contractId) {
    return this.http.get<any>(`${this.contractsBaseUrl}/${contractId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getByUserId() {
    return this.http.get<any>(`${this.contractsBaseUrl}/usercontracts`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  updateContract(guid, body) {
    return this.http.put<any>(`${this.contractsBaseUrl}/${guid}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createContract(body) {
    return this.http.post<any>(`${this.contractsBaseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  deleteContract(contractId) {
    return this.http.delete<any>(`${this.contractsBaseUrl}/${contractId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  setContractStatus(contractId, status) {
    return this.http.patch<any>(`${contractId}?status=${status}`, this.options, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  checkClient(contractId, cnpjId) {
    return this.http.get<any>(`${this.contractsBaseUrl}/GetContractData/${contractId}/cliente/${cnpjId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

}
