import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpService } from 'src/app/services/http/http.service';
import Swal from 'sweetalert2'
import { API } from 'src/app/services/config';
import { ContractsService } from 'src/app/services/http-requests';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-contrato-cadastro-edit',
  templateUrl: './contrato-cadastro-edit.component.html',
  styleUrls: ['./contrato-cadastro-edit.component.scss'],
  providers: [FormBuilder]
})
export class ContratoCadastroEditComponent implements OnInit {
  checked = false
  isEditing
  contracts
  contractStatus
  inputStatus
  enabledContract = false;
  statusToSend = 2
  haveContract: boolean; //create or edit?
  private validCNPJ = false;
  private previousRoute: string;
  private alreadyFiltered: boolean;
  private isEnable = false;
  private isSaving = false;
  private guid
  private contractSituation
  private idContract;
  private enableDate;
  contract: FormGroup;
  private idContractError: string;
  private contractToSend: { contrato_tokpag: any; cnpj: any; razao_social: any; nome_loja: any; situacao_contrato: string; };

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private httpService: HttpService,
    private contractService: ContractsService,
    private swalService: SwalService
  ) { }

  ngOnInit() {
    localStorage.setItem('currentUri', 'contratos/listar')
    this.contract = this.fb.group({
      'idContract': [null, Validators.required],
      'idClient': [null, Validators.required],
      'CNPJ': [null, Validators.required],
      'socialReason': [null, Validators.required],
      'fantasyName': [null, Validators.required],
      'contractSituation': [false, Validators.required],
    })

    this.contract.valueChanges.subscribe();
    this.getExistentContractInfo();
  }

  getExistentContractInfo() {
    this.haveContract = this.router.url == '/contratos/cadastro' ? false : true

    if (this.haveContract) {

      this.route.params.subscribe((params) => {
        let dependence = JSON.parse(params.dependence)
        this.guid = dependence.guid
        this.idContract = dependence.idContract
        this.contractSituation = dependence.stId
        this.contract.get('idContract').setValue(this.idContract);
        this.contractService.getOneContract(dependence.idContract).subscribe(res => {
          this.contracts = res.data
          this.fillFields(res, 'api')
          this.statusChange(res.data.situacao_contrato)
        })
        this.contractSituation == 'Inativo' ? this.isEnable = false : this.isEnable = true

        if (!this.isEnable) {
          this.contract.controls['contractSituation'].disable()
        }
      })
    } else {
      this.contract.controls['contractSituation'].disable()
    }
  }

  statusChange(evt) {
    if (evt == 2) {
      this.inputStatus = true
      this.statusToSend = 2
    } else if (evt == 0) {
      this.inputStatus = false
      this.statusToSend = 0
    } else {
      this.contractStatus = evt.target.checked
      if (this.contractStatus) {
        this.inputStatus = true
        this.statusToSend = 2
      } else if (!this.contractStatus) {
        this.inputStatus = false
        this.statusToSend = 0
      }
    }
    return this.statusToSend
  }

  btnSalvar() {
    let contract = {
      "contrato_tokpag": this.contract.get('idContract').value,
      "cnpj": this.contract.get('CNPJ').value,
      "razao_social": this.contract.get('socialReason').value,
      "nome_loja": this.contract.get('fantasyName').value,
      "situacao_contrato": this.statusToSend
    }

    if (this.haveContract) {
      this.contractService.updateContract(this.idContract, contract).subscribe(res => {
        setTimeout(() => {
          this.isSaving = false
        }, 2000);
        if (res.sucesso || res.status == 200 || res.status == 201) {
          this.swalService.success(res.mensagem, this.router.navigate(['/contratos/listar']))
        }
      })
    } else {
      this.contractService.createContract(contract).subscribe(res => {
        setTimeout(() => {
          this.isSaving = false
        }, 2000);
        if (res.sucesso || res.status == 200 || res.status == 201) {
          this.swalService.success(res.mensagem, this.router.navigate(['/contratos/listar']))
        }
      })
    }
  }

  contractMessage() {

    const idContract = this.contract.controls['idContract'].value;
    if (idContract.length != 36) {
      this.idContractError = 'Contrato no cadastradO'

    }
  }

  enableContract() {

    this.contractService.setContractStatus(this.idContract, this.isEnable ? 'Inativo' : 'Ativo').subscribe(res => {
      this.isEnable = !this.isEnable;
    })

    if (this.isEnable) {
      this.contract.controls['contractSituation'].enable()
      this.contract.controls['contractSituation'].setValue(true)
    } else {
      this.contract.controls['contractSituation'].setValue(false)

      this.contract.controls['contractSituation'].disable()
    }

    this.contractToSend = {
      contrato_tokpag: this.contract.controls['idContract'].value,
      cnpj: this.contract.controls['CNPJ'].value,
      razao_social: this.contract.controls['socialReason'].value,
      nome_loja: this.contract.controls['fantasyName'].value,
      situacao_contrato: this.isEnable ? 'Ativo' : 'Inativo'
    }

    this.contractService.updateContract(this.guid, this.contractToSend).subscribe(res => {
      if (!res.sucesso) {
        this.swalService.error(res.mensagem)
      }
    })

  }

  cleanFilds() {
    this.contract.controls['idClient'].setValue('');
    this.contract.controls['socialReason'].setValue('')
  }

  fillFields(res, api) {

    if (api == 'itau') {
      this.contract.controls['idClient'].setValue(res.data.cliente.cnpj);
      this.contract.controls['socialReason'].setValue(res.data.cliente.razao_social)
      res.data.status == "Ativo" ? this.isEnable = true : this.isEnable = false
    } else {
      this.contract.controls['idClient'].setValue(res.cnpj)
      this.contract.controls['CNPJ'].setValue(res.cnpj)
      this.contract.controls['socialReason'].setValue(res.razao_social)
      this.contract.controls['fantasyName'].setValue(res.nome_loja)
      this.enableDate = res.data_ativacao
    }

  }


  validarCNPJ() {
    let cnpj: string = this.contract.controls['CNPJ'].value;

    if (cnpj == '') {
      this.validCNPJ = false;

      return false
    }

    if (cnpj.length != 14) {

      this.validCNPJ = false;
      return false
    }

    // Elimina CNPJs invalidos conhecidos
    if (cnpj == "00000000000000" ||
      cnpj == "11111111111111" ||
      cnpj == "22222222222222" ||
      cnpj == "33333333333333" ||
      cnpj == "44444444444444" ||
      cnpj == "55555555555555" ||
      cnpj == "66666666666666" ||
      cnpj == "77777777777777" ||
      cnpj == "88888888888888" ||
      cnpj == "99999999999999") {

      this.validCNPJ = false;
      return false
    }

    // Valida DVs
    let tamanho = cnpj.length - 2
    let numeros: any = cnpj.substring(0, tamanho);
    let digitos = cnpj.substring(tamanho);
    let soma = 0;
    let pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
        pos = 9;
    }
    let resultado: any = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(0)) {

      this.validCNPJ = false;
      return false
    }

    tamanho = tamanho + 1;
    numeros = cnpj.substring(0, tamanho);
    soma = 0;
    pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
        pos = 9;
    }
    resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(1)) {
      this.validCNPJ = false;
      return false
    }

    this.validCNPJ = true;
    return true

  }

  get setIdContractValue() {
    return this.contract.get('idContract').value
  }

  get setContractCnpjValue() {
    return this.contract.get('CNPJ').value
  }

  get setContractSocialReasonValue() {
    return this.contract.get('socialReason').value
  }
  get setContractFantasyNameValue() {
    return this.contract.get('fantasyName').value
  }

  checkContract() {
    let id = this.contract.get('idContract').value
    let cnpj = this.contract.get('CNPJ').value
    this.contractService.checkClient(id, cnpj).subscribe(res => {
      if(res.sucesso) {
        this.checked = true
      }
    })
  }

}
