import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  baseUrl = API + '/api/dasboard'

  constructor(private http: HttpClient) { }

  getCountResults(fDate, tDate) {
    const category = 'count';
    return this.http.get<any>( `${this.baseUrl}/${category}?fromDate=${fDate}&toDate=${tDate}`, {headers: HEADER} )
      .pipe(
        tap()
      )
  }

  getSumResults(fDate, tDate) {
    const category = 'sum';
    return this.http.get<any>( `${this.baseUrl}/${category}?fromDate=${fDate}&toDate=${tDate}`, {headers: HEADER} )
      .pipe(
        tap()
      )
  }

  getActiveSuum() {
    return this.http.get<any>(`${this.baseUrl}/activecontractstorecashier`, {headers: HEADER})
      .pipe(
        tap()
      )
  }

  getActiveData(userId) {
    return this.http.get<any>(`${this.baseUrl}/activecontractstorecashier/${userId}`, {headers: HEADER})
      .pipe(
        tap()
      )
  }


}
