import { Component, OnInit } from '@angular/core';
import { HttpService } from 'src/app/services/http/http.service';
import { ActivatedRoute, Router } from '@angular/router';
import Swal from 'sweetalert2'
import { API } from 'src/app/services/config';
import { store } from '@angular/core/src/render3';
import { UserService } from 'src/app/services/http-requests/user/user.service';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-listar',
  templateUrl: './listar.component.html',
  styleUrls: ['./listar.component.scss']
})
export class ListarComponent implements OnInit {

  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }

  usersList
  page
  showList = false;

  totalItems = null;
  timeout = null;
  filterLoaded = false;
  storeSelected;
  modalDeleteOpened = false
  skip = 0;
  readonly take = 10;
  contractsIds: any;
  lastContractId

  filterInput = {
    contractId: 'Identificao do Contrato',
    socialReason: ''
  }

  storesTitle = [
    'id',
    'Nome',
    'Grupo',
    'Funo',
    'Login',
    'Status'
  ]

  stores;
  storesTest = {
    storeId: '100.200',
    storeName: 'Primeira Loja',
    storeSituation: 'Criada',
    CNPJ: '20.333/444/0020-10',
    cashierQtd: 10,
  }
  p: number;

  constructor(
    private userService: UserService,
    private router: Router,
    private swalService: SwalService
  ) { }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for(let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if(permissionsArr.includes('usuarios')) {
          localStorage.setItem('currentUri', 'usuarios/listar')
          this.canView = true
          this.router.navigateByUrl('/usuarios')
          this.loadUsers()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  loadUsers() {
    this.userService.listUsers().subscribe(res => {
      this.usersList = res.data
      this.showList = true
    })
  }

  deleteUser(userId, name) {
    this.swalService.deleteUserSwal(userId, name)
  }

  selectStore(storeId) {
    this.storeSelected = storeId;
  }

  idEditarUsuario(id) {
    localStorage.setItem('idEditarUsuario', id);
  }

  createUser() {
    this.router.navigateByUrl('/usuarios/cadastro');
  }

  inputFilter(evt) {
    let returned: any[] = []
    setTimeout(() => {
      this.userService.listUsers().subscribe(res => {
        for (let i = 0; i < res.data.length; i++) {
          if (res.data[i].nome.includes(evt)) {
            returned.push(res.data[i])
          }
        }
      })
      this.usersList = returned
      this.showList = true
    }, 500)
  }

  getStatusToFilter(evt) {
    let returned: any[] = []
    this.userService.listUsers().subscribe(res => {
      if (evt === '2' || evt === '0') {
        let parsedStatus = parseInt(evt)
        let returned: any[] = []
        for (let i = 0; i < res.data.length; i++) {
          if (parsedStatus == res.data[i].status) {
            returned.push(res.data[i])
          }
        }
        this.usersList = returned
        this.showList = true
      } else {
        this.usersList = res.data
        this.showList = true
      }
    })
  }

}