import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';
import { headersToString } from 'selenium-webdriver/http';
import { helpers } from 'chart.js';

@Injectable({
  providedIn: 'root'
})
export class ReportsService {


  baseUrl = API + '/api/report/getparameters'

  constructor(private http: HttpClient) { }

  getAllContractsList(initDate: string, endDate: string, contractId: string, storeID: string, cashierId: string, status: string) {
    let parans = {
      "fromDate": initDate,
      "toDate": endDate,
      "ContractTokPag": contractId,
      "storeId": storeID,
      "cashierId": cashierId,
      "status": status
    }
    return this.http.get<any>(`${this.baseUrl}`, { headers: HEADER, params: parans })
      .pipe(
        tap()
      )
  }

  getLogs() {
    return this.http.get<any>(`${API}/api/logdiscount?skip=0&take=0`, {headers: HEADER})
      .pipe(
        tap()
      )
  }

}
