import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpService } from 'src/app/services/http/http.service';
import Swal from 'sweetalert2'
import { API } from 'src/app/services/config';
import { CashiersService } from 'src/app/services/http-requests';
import { SwalService } from 'src/app/services/swal/swal.service';

@Component({
  selector: 'app-caixa-cadastro-edit',
  templateUrl: './caixa-cadastro-edit.component.html',
  styleUrls: ['./caixa-cadastro-edit.component.scss']
})
export class CaixaCadastroEditComponent implements OnInit {
  inputStatus
  statusToSend = 2
  storetStatus
  errMessage = 'Algo deu errado, Tente mais tarde!'
  isEditing = false

  private isCreate: boolean; //create or edit?
  private isSaving = false;
  private cashier: FormGroup;
  private contractId: any;
  private storeId: any;
  private contractsIds: any;
  private lastContractId: any;
  private idCashier;
  private modalConfirmQrCodeOpened = false;
  private objectKeys = Object.keys

  private filterInput = {
    storeId: 'Identificao da Loja',
    contractId: 'Identificao do Contrato'
  }
  timeout: any;
  storesIds: any;


  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private httpService: HttpService,
    private cashierService: CashiersService,
    private swalService: SwalService
  ) {
    this.cashier = fb.group({
      'idCashier': '',
      'description': [null, Validators.required],
      'status': [false, Validators.required],
      'hash': '',
      'qrCode': ''
    })
  }

  async ngOnInit() {
    localStorage.setItem('currentUri', 'caixas/listar')
    this.isCreate = this.router.url.split('/')[2] == 'cadastro' ? true : false

    this.lastContractId = this.filterInput.contractId
    this.cashier.controls['idCashier'].disable()
    this.cashier.controls['hash'].disable()
    this.cashier.controls['qrCode'].disable()

    if (this.isCreate) {

      this.cashier.controls['status'].disable()

      this.route.params.subscribe(async params => {

        let dependence = JSON.parse(params.dependence)
        this.contractId = dependence.contractId
        this.storeId = dependence.storeId
        this.filterInput.storeId = this.storeId
        this.filterInput.contractId = this.contractId

        this.contractsIds = await this.httpService.get(`${API}/api/store/getStoresContractsIds/` + localStorage.getItem('idusuario'));
        if (this.contractsIds == 'error') {
          this.swalService.error(this.errMessage)
        }
        this.storesIds = await this.httpService.get(`${API}/api/store/getAllStoresIdByContractTokPag?contractTokPag=${this.filterInput.contractId}`)

        if (this.storesIds == 'error') {
          this.swalService.error(this.errMessage)
        }
        let res = await this.httpService.get(`${API}/api/store/${this.storesIds[this.filterInput.storeId]}`)
        if (res == 'error') {
          this.swalService.error(this.errMessage)
        }
        if (res.status_loja == "Ativo") {
          this.cashier.controls['status'].enable()

        }

      })

    } else {

      this.route.params.subscribe(async params => {
        let dependence = JSON.parse(params.dependence)

        this.idCashier = dependence.cashierId
        this.storeId = dependence.storeId
        this.contractId = dependence.contractId
        this.filterInput.storeId = this.storeId
        this.filterInput.contractId = this.contractId
        this.storesIds = await this.httpService.get(`${API}/api/store/getAllStoresIdByContractTokPag?contractTokPag=${this.filterInput.contractId}`)

        this.cashier.controls['idCashier'].setValue(this.idCashier);

        this.fillFields()
      })


    } 

    if(this.idCashier) {
      this.isEditing = true
    }
  }

  publish() {
    let cashier: any = {
      id_loja: this.filterInput.storeId,
      descricao_caixa: this.cashier.get('description').value,
      status_caixa: this.statusToSend,
    }

    if (this.isCreate) {
      this.cashierService.createCashier(cashier).subscribe(res => {
        if (res.sucesso || res.status == 200 || res.status == 201) {
          this.swalService.success(res.mensagem, this.router.navigate(['/caixas/listar', { storeId: this.storesIds[this.filterInput.storeId], contractId: this.contractId }]))        
        } else if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        }
      })
    } else {
      cashier.id_caixa = this.idCashier
      this.cashierService.updateCashier(this.idCashier, cashier).subscribe(res => {
        if (res.sucesso || res.status == 200 || res.status == 201) {
          this.swalService.success(res.mensagem, this.router.navigate(['/caixas/listar', { storeId: this.storesIds[this.filterInput.storeId], contractId: this.contractId }]))        
        } else if (!res.sucesso) {
          this.swalService.error(res.mensagem)
        }
      })
    }

  }

  statusChange(evt) {
    if (evt == 2) {
      this.inputStatus = true
      this.statusToSend = 2
    } else if (evt == 0) {
      this.inputStatus = false
      this.statusToSend = 0
    } else {
      this.storetStatus = evt.target.checked
      if (this.storetStatus) {
        this.inputStatus = true
        this.statusToSend = 2
      } else if (!this.storetStatus) {
        this.inputStatus = false
        this.statusToSend = 0
      }
    }
    return this.statusToSend
  }

  async fillFields() {
    let res = await this.httpService.get(`${API}/api/cashier/${this.idCashier}`)
    if (res == 'error') {
      this.swalService.error(res.mensagem)
    }
    this.statusChange(res.data.status_caixa)
    this.cashier.controls['description'].setValue(res.data.descricao_caixa)
    this.cashier.controls['status'].setValue(res.data.status_caixa == 'Ativo' ? true : false)
    this.cashier.controls['hash'].setValue(res.data.id_qrcode)
    this.cashier.controls['qrCode'].setValue(res.data.qrcode)
  }

  inputFilter() {
    clearTimeout(this.timeout)
    this.timeout = setTimeout(async () => {

      if (this.lastContractId != this.filterInput.contractId) {
        this.storesIds = await this.httpService.get(`${API}/api/store/getAllStoresIdByContractTokPag?contractTokPag=${this.filterInput.contractId}`)
        if (this.storesIds == 'error') {
          this.swalService.error(this.errMessage)
        }
        this.lastContractId = this.filterInput.contractId
      }
    }, 1000)
  }

  async newQrCode() {
    this.modalConfirmQrCodeOpened = !this.modalConfirmQrCodeOpened
    let res = await this.httpService.post(`${API}/api/cashier/GenerateQRCode?cashierId=${this.idCashier}`, {})

    if (res.status < 200 || res.status > 299) {
      this.swalService.error(res.mensagem)
    }
    this.fillFields()

  }

}