import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { CashiersService, ContractsService, StoresService } from 'src/app/services/http-requests';
import { ReportsService } from 'src/app/services/http-requests/reports/reports.service';
import { HttpService } from 'src/app/services/http/http.service';
import { ExcelService } from 'src/app/services/excel/excel.service';
import { UserService } from 'src/app/services/http-requests/user/user.service';

@Component({
  selector: 'app-relatorios',
  templateUrl: './relatorios.component.html',
  styleUrls: ['./relatorios.component.scss']
})
export class RelatoriosComponent implements OnInit {
  showList = false;
  collection = [];

  page
  resultsList
  canView = false

  user = {
    userId: localStorage.getItem('idusuario'),
    userName: localStorage.getItem('nomeusuario')
  }
  storeInput
  cashierInput
  initDate
  endDateInput

  selectedEndDate
  selectedInitDate;
  cashiersList;
  contracts;
  contractInput = '';
  storesList;

  dataTitle =
    [
      'Descrio do contrato',
      'Descrio da loja',
      'Descrio do caixa',
      'Data do pagamento',
      'Valor do pagamento',
      'Status',
    ]
  items;
  data: any = [
    {
      id: '1',
      primeiro: 'ravi',
      segundo: 1000,
      terceiro: 1000,
      quarto: 1000,
      quinto: 1000,
    },
    {
      id: '2',
      primeiro: 'marcelo',
      segundo: 2000,
      terceiro: 5000,
      quarto: 7000,
      quinto: 6000,
    },
    {
      id: '3',
      primeiro: 'renata',
      segundo: 1000,
      terceiro: 1000,
      quarto: 2000,
      quinto: 1000,
    },
    {
      id: '4',
      primeiro: 'joao',
      segundo: 1000,
      terceiro: 10,
      quarto: 1000,
      quinto: 3,
    },
  ];

  cashiers = []
  totalItems = null;
  timeout = null;
  modalQrOpened = false;
  actualImage;
  skip = 0;
  readonly take = 10;

  modalDeleteOpened = false;
  qrCodeSelected;
  descriptionSelected;
  cashierSelected: string;
  filterLoaded: boolean;
  isContractLoaded: boolean = false
  contractsIds = undefined
  storesIds = undefined
  storeValue
  objectKeys = Object.keys

  hoveredDate: NgbDate;
  fromDate: NgbDate;
  toDate: NgbDate;

  filterInput = {
    code: '',
    storeId: 'Identificao da Loja',
    contractId: 'Identificao do Contrato'
  }
  lastStoreId
  lastContractId
  lastCode

  // constructor() { }
  constructor(
    private cashierService: CashiersService,
    private reportService: ReportsService,
    private storesService: StoresService,
    private contractsService: ContractsService,
    private excelService: ExcelService,
    private calendar: NgbCalendar,
    private router: Router,
    private userService: UserService
  ) {
    for (let i = 1; i <= 100; i++) {
      this.collection.push(`item ${i}`);
    }
    this.fromDate = calendar.getToday();
    this.toDate = calendar.getNext(calendar.getToday(), 'd', 10);
  }

  ngOnInit() {
    this.haveNoUser()
  }

  haveNoUser() {
    if (!this.user.userId) {
      this.router.navigateByUrl('/login')
    } else {
      this.userService.getPermissions(this.user.userId).subscribe(res => {
        let permissionsArr: any[] = []
        for(let i = 0; i < res.data.length; i++) {
          permissionsArr.push(res.data[i].path)
        }
        if(permissionsArr.includes('relatorios-transacionais')) {
          localStorage.setItem('currentUri', 'relatorios-transacionais/listar')
          this.canView = true
          this.router.navigateByUrl('/relatorios-transacionais')
          this.initFuncs()
        } else {
          this.router.navigateByUrl('/not-allowed')
        }
      })
    }
  }

  initFuncs() {
    this.getcontracts();
    this.reportService.getAllContractsList('2019-01-01', '2019-01-27', '', '', '', '').subscribe(res => {
      this.resultsList = res.data;
      this.showList = true
    })
  }

  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
  }

  isHovered(date: NgbDate) {
    return this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.fromDate) && date.before(this.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.fromDate) || date.equals(this.toDate) || this.isInside(date) || this.isHovered(date);
  }

  getcontracts() {
    this.contractsService.getAllContractsInfo().subscribe(res => {
      this.contracts = res.data.contracts
    })
  }

  saveSelectedContract(contractId) {
    this.storesService.getContractsList(contractId).subscribe(res => {
      this.storesList = res.data
    })
  }

  saveSelectedStore(storeId) {
    this.cashierService.getCashiersList(storeId).subscribe(res => {
      this.cashiersList = res.data.cashier
    })
  }

  saveInitDate(evt) {
    this.selectedInitDate = evt;
  }

  saveEndDate(evt) {
    this.selectedEndDate = evt;
  }

  getReports(initDate, endDate, contract, store, cashier, status) {
    this.showList = false
    this.reportService.getAllContractsList(initDate, endDate, contract, store, cashier, status).subscribe(res => {
      this.resultsList = res.data;
      this.showList = true
    })
  }

  export(evt) {
    this.excelService.exportAsExcelFile(evt, 'sample');
  }

}
