import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from 'src/app/services/http-requests/user/user.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  private user: FormGroup;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {
    this.user = this.fb.group({
      'name': ['', [Validators.required, Validators.email]],
      'password': ['', [Validators.required]],
    })
  }

  login() {
    let user = this.user.controls['name'].value
    let password = this.user.controls['password'].value
    localStorage.clear()

    this.userService.userLogin(user, password).subscribe(res => {
      if (res.sucesso == true) {
        localStorage.setItem('userToken', res.data.token)
        if (localStorage.getItem('userToken')) {
          this.userService.getUserDetails(res.data.token).subscribe(tokenRes => {
            if (tokenRes.sucesso) {
              localStorage.setItem('currentUser', JSON.stringify({ token: res.data.token }))
              localStorage.setItem('idusuario', tokenRes.data.id_usuario)
              localStorage.setItem('funcao', tokenRes.data.funcao)
              localStorage.setItem('grupo', tokenRes.data.grupo_id)
              localStorage.setItem('nomeusuario', tokenRes.data.nome)
              this.userService.getPermissions(tokenRes.data.id_usuario).subscribe(permissionRes => {
                let route = permissionRes.data[0].path
                localStorage.setItem('mainUserUri', route)
                setTimeout(() => {
                  this.router.navigateByUrl(`/${route}`)
                }, 500)
              })
            } else {
              Swal('Oops...', 'Desculpe o problema, tente novamente', 'error')
            }
          })
        }
      } else {
        Swal('Oops...', 'Login ou senha invlidos!', 'error')
      }
    })
  }

}
