import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';

@Component({
  selector: 'app-permissions-configurations',
  templateUrl: './permissions-configurations.component.html',
  styleUrls: ['./permissions-configurations.component.scss']
})
export class PermissionsConfigurationsComponent implements OnInit {

  permission = [
    {
      group: [
        {
          id: '1',
          name: 'Linx',
          roles: [
            {
              id: '1',
              name: 'Administrador Linx',
              permissions: [
                {
                  id: '1',
                  name: 'Dashboard',
                  path: 'dashboard'
                },
                {
                  id: '2',
                  name: 'Contratos',
                  path: 'contratos'
                }
              ]
            },
            {
              id: '2',
              name: 'Default Linx',
              permissions: [
                {
                  id: '1',
                  name: 'Dashboard',
                  path: 'dashboard'
                },
                {
                  id: '2',
                  name: 'Contratos',
                  path: 'contratos'
                },
                {
                  id: '3',
                  name: 'Lojas',
                  path: 'lojas'
                }
              ]
            }
          ]
        },
        {
          id: '2',
          name: 'Ita',
          roles: [
            {
              id: '1',
              name: 'Administrador Ita',
              permissions: [
                {
                  id: '1',
                  name: 'Dashboard',
                  path: 'dashboard'
                },
                {
                  id: '2',
                  name: 'Contratos',
                  path: 'contratos'
                }
              ]
            },
            {
              id: '2',
              name: 'Default Ita',
              permissions: [
                {
                  id: '1',
                  name: 'Dashboard',
                  path: 'dashboard'
                },
                {
                  id: '2',
                  name: 'Contratos',
                  path: 'contratos'
                },
                {
                  id: '3',
                  name: 'Lojas',
                  path: 'lojas'
                }
              ]
            }
          ]
        }
      ]
    }
  ]

  filterGroup
  selectedGroup = {
    id: '',
    name: ''
  }
  filterRoles
  selectedRole = {
    id: '',
    name: ''
  }
  showRoles = false
  filterPermissions
  showPermissions = false

  constructor() { }

  ngOnInit() {
    this.getGroups()
  }

  getGroups() {
    this.permission.filter(g => {
      this.filterGroup = g.group
    })
  }

  viewgroupRoles(id, name) {
    this.selectedGroup.id = id
    this.selectedGroup.name = name
    this.permission.filter(g => {
      g.group.filter(r => {
        if (id === r.id) {
          this.filterRoles = r.roles
          this.showRoles = true
        }
      })
    })
  }

  viewRolesPermissions(id, name) {
    this.selectedRole.id = id
    this.selectedRole.name = name
    this.permission.filter(g => {
      g.group.filter(r => {
        if (this.selectedGroup.id === r.id) {
          r.roles.filter(p => {
            if (id === p.id) {
              this.filterPermissions = p.permissions
              this.showPermissions = true
            }
          })
        }
      })
    })
  }

  createGroup() {
    swal({
      text: 'Nome do grupo',
      input: "text",
      confirmButtonText: 'Criar grupo',
      showLoaderOnConfirm: true,
    }).then((response) => {
      if (response.value !== '') {
        swal({
          text: `Obrigado por inserir o nome do grupo ${response.value}`,
          type: 'success'
        })
      } else if (response.value === '') {
        swal({
          text: `Por favor insira um nome`,
          type: 'error'
        })
      }
    })
  }

  createRole() {
    swal({
      text: 'Nome da funo',
      input: "text",
      confirmButtonText: 'Criar funo',
      showLoaderOnConfirm: true,
    }).then((response) => {
      if (response.value !== '') {
        swal({
          text: `Obrigado por inserir o nome da funo ${response.value} no grupo ${this.selectedGroup.name}`,
          type: 'success'
        })
      } else if (response.value === '') {
        swal({
          text: `Por favor insira um nome`,
          type: 'error'
        })
      }
    })
  }

  createPermission() {
    swal({
      text: 'Nome da permisso',
      input: "text",
      confirmButtonText: 'Criar permisso',
      showLoaderOnConfirm: true,
    }).then((response) => {
      if (response.value !== '') {
        swal({
          text: `Obrigado por inserir o nome da permisso ${response.value} na funo ${this.selectedRole.name}`,
          type: 'success'
        })
      } else if (response.value === '') {
        swal({
          text: `Por favor insira um nome`,
          type: 'error'
        })
      }
    })
  }

}
