import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap } from 'rxjs/operators';
import { API, HEADER } from '../../config';

@Injectable({
  providedIn: 'root'
})
export class DescontosService {

  baseUrl = API + '/api/discount'
  configBaseUrl = API + '/api/storeconfiguration/discount'

  constructor(private http: HttpClient) { }

  getDescount(storeId) {
    return this.http.get<any>(`${this.baseUrl}/getdiscounts/${storeId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  getDiscountById(storeId, discountId) {
    return this.http.get<any>(`${this.baseUrl}/GetDiscount/${storeId}/${discountId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createDiscount(body) {
    return this.http.post<any>(`${this.baseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  updateDiscount(discountId, body) {
    return this.http.put<any>(`${this.baseUrl}/${discountId}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  deleteDescount(id) {
    return this.http.delete<any>(`${this.baseUrl}/${id}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createDisconfigByContract(contractId, body) {
    return this.http.post<any>(`${this.configBaseUrl}/${contractId}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  listDisconfigByContract(contractId) {
    return this.http.get<any>(`${this.configBaseUrl}/contract/${contractId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  createDisconfigByStore(body) {
    return this.http.post<any>(`${this.configBaseUrl}`, body, { headers: HEADER })
      .pipe(
        tap()
      )
  }

  listDisconfigByStore(storeId) {
    return this.http.get<any>(`${this.configBaseUrl}/store/${storeId}`, { headers: HEADER })
      .pipe(
        tap()
      )
  }



}
